                          readonly
                          v-on="on"
                        />
                      </template>
                      <v-time-picker
                        v-if="menu1"
                        v-model="agendamento.horario"
                        format="24hr"
                        data-vv-name="horario"
                        full-width
                        @click:minute="$refs.menu.save(agendamento.horario)"
                      />
                    </v-menu>
                  </v-flex>
                  <v-flex
                    xs12
                    sm3
                    md3
                  >
                    <v-text-field
                      v-model="dataFormated"
                      name="data"
                      label="Data"
                      readonly
                    />
                  </v-flex>
                  <v-flex
                    xs12
                    md9
                  >
                    <v-text-field
                      name="name"
                      label="Localidade - Horário"
                      :value="`${viagem.localidade.nome}  -  ${moment(viagem.horario, 'HH:mm:ss').format('HH:mm')}`"
                      readonly
                    />
                  </v-flex>
                  <v-flex xs12>
                    <v-autocomplete
                      v-model="agendamento.local_de_consulta_id"
                      v-validate="'required'"
                      name="local_de_consulta_id"
                      data-vv-name="local_de_consulta_id"
                      :error-messages="errors.first('local_de_consulta_id')"
                      :items="locais"
                      label="* Locais de consulta"
                      item-text="nome"
                      item-value="id"
                    />
                  </v-flex>
                  <v-flex
                    xs12
                    sm6
                    md6
                  >
                    <v-autocomplete
                      v-model="agendamento.especialidade_id"
                      v-validate="'required'"
                      name="especialidade_id"
                      data-vv-name="especialidade_id"
                      :error-messages="errors.first('especialidade_id')"
                      :items="especialidades"
                      item-text="nome"
                      item-value="id"
                      label="* Especialidade"
                    />
                  </v-flex>
                  <v-flex
                    xs12
                    sm6
                    md6
                  >
                    <v-autocomplete
                      v-model="agendamento.cid_id"
                      v-validate="'required'"
                      name="cid_id"
                      data-vv-name="cid_id"
                      :error-messages="errors.first('cid_id')"
                      :items="cids"
                      item-text="nome"
                      item-value="id"
                      label="* Cid10"
                    />
                  </v-flex>
                  <v-flex xs12>
                    <v-autocomplete
                      v-model="agendamento.procedimentos"
                      v-validate="'required'"
                      data-vv-name="procedimentos"
                      name="procedimentos"
                      :items="procedimentos"
                      chips
                      color="blue-grey lighten-2"
                      label="* Procedimentos"
                      item-text="nome"
                      item-value="id"
                      :error-messages="errors.first('procedimentos')"
                      multiple
                    >
                      <template
                        slot="selection"
                        slot-scope="data"
                      >
                        <v-chip
                          :selected="data.selected"
                          close
                          class="chip--select-multi"
                          @input="data.parent.selectItem(data.item)"
                        >
                          {{ data.item.nome }}
                        </v-chip>
                      </template>
                      <template
                        slot="item"
                        slot-scope="data"
                      >
                        <template>
                          <v-list-tile-content>
                            <v-list-tile-title v-html="data.item.nome" />
                          </v-list-tile-content>
                        </template>
                      </template>
                    </v-autocomplete>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field
                      v-model="agendamento.acompanhante"
                      name="acompanhante"
