                      :error-messages="errors.first('acompanhante')"
                    />
                  </v-flex>
                  <v-flex xs12>
                    <v-btn
                      v-if="viagem.status == 'Agendada'"
                      color="blue darken-1"
                      flat
                      outline
                      @click="salvar"
