              </v-container>
            </v-card>
          </v-dialog>
        </v-toolbar>
        <!-- Mensagem de confirmação para deletar-->
        <template>
          <div>
            <v-layout
              row
              justify-center
            >
              <v-dialog
                v-model="dialogConfirmDelete"
                persistent
                max-width="290"
              >
                <v-card>
                  <v-card-title
                    class="error headline"
                    style="font-weight: bold; color: white"
                  />
