                    Você deseja realmente deletar este agendamento?
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer />
                    <v-btn
                      flat
                      @click="dialogConfirmDelete = false"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                      flat
                      color="error darken-1"
                      @click="deletar()"
                    >
                      Confirmar
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-layout>
          </div>
        </template>
        <!-- LISTAR PACIENTES AGENDADOS -->
        <template>
          <v-data-table
            :headers="headers"
            :items="agendamentos"
            flat
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td class="text-xs-left">
                {{ props.item.paciente.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.horario }}
              </td>
              <td class="text-xs-left">
                {{ props.item.localDeConsulta.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.paciente.celular }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  v-if="viagem.status == 'Agendada'"
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  v-if="viagem.status == 'Agendada'"
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
