<script>
import Viagem from '../../services/viagens';
import Agendamento from '../../services/agendamentos';
import Localidade from '../../services/localidades';
import LocalDeConsulta from '../../services/locaisDeConsulta';
import Cid from '../../services/cids';
import Procedimento from '../../services/procedimentos';
import Especialidade from '../../services/especialidades';
import Paciente from '../../services/pacientes';

import HelpAgendamento from './HelpAgendamento.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpAgendamento,
  },
  data: () => ({
    alterar: false,
    alertS: false,
    alertE: false,
    message: '',
    dialogConfirmDelete: false,
    dialog: false,
    menu2: false,
    menu1: false,
    headers: [
      { text: 'Paciente', value: 'paciente' },
      { text: 'Horário', value: 'horario' },
      { text: 'Local de consulta', value: 'local' },
      { text: 'Celular', value: 'celular' },
    ],
    dataAgendamento: null,
    localidade: null,
    localidades: [],
    procedimentos: [],
    especialidades: [],
    cids: [],
    pacientes: [],
    viagens: [],
    locais: [],
    agendamento_id: 0,
    agendamento: {
      paciente_id: null,
      data: null,
      horario: null,
      local_de_consulta_id: null,
      especialidade_id: null,
      cid_id: null,
      procedimentos: [],
      viagem_id: null,
      acompanhante: '',
    },
    temAgendamento: false,
    temAcompanhante: false,
    viagem: {
      id: null,
      data: null,
      horario: null,
      localidade_id: null,
      localidade: { nome: '' },
      motorista_id: null,
      motorista: { nome: '' },
      veiculo_id: null,
      veiculo: {
        numMod: null,
        capacidade: null,
      },
      agendamentos: [],
      status: null,
    },
    agendamentos: [],
    vagos: 0,
    ocupados: 0,
    capacidade: 0,
    dictionary: {
      custom: {
        paciente_id: {
          required: 'O paciente deve ser informado',
        },
        horario: {
          required: 'O horário deve ser informado',
        },
        local_de_consulta_id: {
          required: 'O local de consulta deve ser informado',
        },
        especialidade_id: {
          required: 'A especialidade deve ser informada',
        },
        cid_id: {
          required: 'O cid deve ser informado',
        },
        procedimentos: {
          required: 'Um ou mais procedimentos devem ser informados',
        },
      },
    },
  }),
  computed: {
    dataFormated() {
      return this.dataAgendamento ? this.moment(this.dataAgendamento).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    dataAgendamento() {
      this.agendamentos = [];
      this.ocupados = 0;
      this.capacidade = 0;
      this.vagos = 0;

      if (this.localidade == null) {
        this.listarViagens(this.dataAgendamento, 0);
      } else {
        this.listarViagens(this.dataAgendamento, this.localidade);
      }
    },
    // eslint-disable-next-line func-names
    localidade() {
      this.listarViagens(this.dataAgendamento, this.localidade);
      this.listarLocais();
    },
    // eslint-disable-next-line func-names
    'agendamento.viagem_id': function () {
      if (this.agendamento.viagem_id != null) {
        this.ocupados = 0;
        this.vagos = 0;
        Viagem.show(this.agendamento.viagem_id).then((result) => {
          this.viagem = result.data;
          this.agendamentos = this.viagem.agendamentos;
          this.localidade = this.viagem.localidade_id;
          this.capacidade = this.viagem.veiculo.capacidade;

          if (this.agendamentos.length > 0) {
            const listaAgendamentos = this.agendamentos;
            listaAgendamentos.forEach((item) => {
              this.ocupados = this.ocupados + 1;
              if (item.acompanhante != null) {
                this.ocupados = this.ocupados + 1;
              }
            });
          }
          this.vagos = this.capacidade - this.ocupados;
        });
      }
    },
    // eslint-disable-next-line func-names
    'agendamento.especialidade_id': function () {
      if (this.agendamento.especialidade_id != null) {
        this.listarCids();
      }
    },
    // eslint-disable-next-line func-names
    'agendamento.paciente_id': function () {
      if (this.viagem.data != null && this.agendamento.paciente_id > 0) {
        this.agendamento.data = this.moment(this.dataFormated, 'DD/MM/YYYY').format('YYYY-MM-DD');
        Agendamento.buscaPorPaciente(this.agendamento.paciente_id, this.agendamento.data, this.agendamento_id)
          .then((result) => {
            if (result.data.length != 0) {
              this.temAgendamento = !this.temAgendamento;
              this.errors.add({
                field: 'paciente_id',
                msg: 'Já existe agendamento para o paciente na data informada',
              });
            }
          });
      }
    },
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);

    this.listarLocalidades();
    this.dataAgendamento = new Date().toISOString().substr(0, 10);

    this.listarEspecialidades();
    this.listarProcedimentos();
    this.listarPacientes();
  },
  methods: {
    removeViagem(item) {
      this.agendamento.viagem_id = null;
      this.ocupados = 0;
      this.vagos = 0;
      this.capacidade = 0;
      this.agendamentos = [];
    },
    listarViagens(data, localidade) {
      this.viagens = [];

      Viagem.buscarPorLocalidadeData(data, localidade)
        .then((result) => {
          result.data.forEach((item) => {
            this.viagem = item;

            this.viagem.veiculo.numMod = `${this.viagem.veiculo.numero} ${this.viagem.veiculo.modelo}`;
            this.viagem.data = this.moment(item.data).format('DD/MM/YYYY');
            this.viagens.push(this.viagem);
          });
        });
    },
    listarAgendamentos() {
      Agendamento.buscaPorViagem(this.viagem.id).then((result) => {
        this.agendamentos = result.data;
      });
    },
    listarLocalidades() {
      Localidade.listar().then((result) => {
        this.localidades = result.data;
      });
    },
    listarLocais() {
      LocalDeConsulta.listarPorLocalidade(this.localidade).then((result) => {
        this.locais = result.data;
      });
    },
    listarEspecialidades() {
      Especialidade.listar().then((result) => {
        this.especialidades = result.data;
      });
    },
    listarCids() {
      Cid.listarPorEspecialidade(this.agendamento.especialidade_id).then((result) => {
        this.cids = result.data;
      });
    },
    listarPacientes() {
      Paciente.listar().then((result) => {
        this.pacientes = result.data;
      });
    },
    listarProcedimentos() {
      Procedimento.listar().then((result) => {
        this.procedimentos = result.data;
      });
    },
    agendarPaciente() {
      this.errors.clear();
      if (this.viagem.status == 'Agendada') {
        if (this.dataAgendamento == null) {
          this.errors.add({
            field: 'data',
            msg: 'Não foi informado a data',
          });
        }

        if (this.viagens.length == 0) {
          this.errors.add({
            field: 'viagem',
            msg: 'Não há viagens para a data selecionada',
          });
        }

        if (this.localidade == null) {
          this.errors.add({
            field: 'localidade',
            msg: 'A localidade deve ser informada',
          });
        }
        if (this.agendamento.viagem_id == null) {
          this.errors.add({
            field: 'viagem',
            msg: 'Não foi selecionada nenhuma viagem',
          });
        }
        if (this.vagos == 0) {
          this.errors.add({
            field: 'viagem',
            msg: 'Não há vagas na viagem selecionada',
          });
        }

        if (this.errors.items.length == 0) {
          this.viagem.localidade_id = this.localidade;
          this.dialog = true;
        }
      } else {
        this.alertS = false;
        this.alertE = true;
        this.message = 'Viagem já foi realizada ou ja passou a data de realização ';
      }
    },
    limparViagem() {
      this.viagem = {
        id: null,
        data: null,
        horario: null,
        localidade_id: null,
        localidade: { nome: '' },
        motorista_id: null,
        motorista: { nome: '' },
        veiculo_id: null,
        veiculo: {
          numMod: null,
          capacidade: null,
        },
      };
      this.agendamentos = [];
      this.ocupados = 0;
      this.capacidade = 0;
    },
    limparAgendamento() {
      this.agendamento_id = 0;
      this.agendamento.paciente_id = null;
      this.agendamento.data = null;
      this.agendamento.horario = null;
      this.agendamento.local_de_consulta_id = null;
      this.agendamento.especialidade_id = null;
      this.agendamento.cid_id = null;
      this.agendamento.procedimentos = [];
      this.agendamento.acompanhante = '';
      this.temAgendamento = false;

      this.errors.clear();
    },
    salvar() {
      let secondValid = true;
      this.$validator.validate().then((valid) => {
        if (this.agendamento.horario != null) {
          const horarioAgendamento = this.moment(this.agendamento.horario, 'HH:mm').format('HH:mm');
          const horarioViagem = this.moment(this.viagem.horario, 'HH:mm:ss').format('HH:mm');
          if (this.moment(horarioAgendamento, 'HH:mm').isBefore(this.moment(horarioViagem, 'HH:mm'))) {
            secondValid = false;
            this.errors.add({
              field: 'horario',
              msg: 'O horário informado é inferior ao horário da viagem',
            });
          }
        }

        if (this.temAgendamento) {
          secondValid = false;

          this.errors.add({
            field: 'paciente_id',
            msg: 'Já existe agendamento para o paciente na data informada',
          });
        }
        if (this.vagos == 0) {
          console.log('temAcompanhante:: ', this.temAcompanhante);
          // eslint-disable-next-line no-mixed-operators
          if (!this.temAcompanhante && this.agendamento.acompanhante != null || this.agendamento.acompanhante != '') {
            secondValid = false;
            this.errors.add({
              field: 'acompanhante',
              msg: 'Não há mais vagas na viagem selecionada',
            });
          }
        }

        if (valid && secondValid) {
          if (this.agendamento_id == 0) {
            Agendamento.salvar(this.agendamento).then((result) => {
              let acompanhante = 0;

              if (this.agendamento.acompanhante != '') {
                acompanhante = 1;
              }

              this.limparAgendamento();
              this.listarAgendamentos();
              this.dialog = false;

              this.ocupados = this.ocupados + 1 + acompanhante;
              this.vagos = this.capacidade - this.ocupados;

              this.message = 'Agendamento realizado com sucesso';
              this.alertS = true;
            });
          } else {
            this.listarAgendamentos();

            if (this.agendamento.procedimentos[0].id) {
              this.agendamento.procedimentos = this.agendamento.procedimentos.map(item => item.id);
            }

            Agendamento.alterar(this.agendamento).then((result) => {
              this.dialog = false;
              let countAcompanhante = 0;

              // eslint-disable-next-line no-mixed-operators
              if (!this.temAcompanhante && this.agendamento.acompanhante != null || this.agendamento.acompanhante != '') {
                countAcompanhante = 1;
                // eslint-disable-next-line no-mixed-operators
              } else if (this.temAcompanhante && this.agendamento.acompanhante == null || this.agendamento.acompanhante == '') {
                countAcompanhante = -1;
              }
              this.ocupados = this.ocupados + (countAcompanhante);
              this.vagos = this.capacidade - this.ocupados;

              this.limparAgendamento();
              this.listarAgendamentos();

              this.message = 'Agendamento alterado com sucesso';
              this.alertS = true;
            });
          }
          this.alterar = false;
        }
      });
    },
    editar(item) {
      this.agendamento = item;
      this.agendamento_id = item.id;
      this.alterar = true;
      if (this.agendamento.acompanhante) {
        this.temAcompanhante = true;
      }
      this.dialog = true;
    },
    messageDelete(item) {
      this.agendamento = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Agendamento.deletar(this.agendamento).then((result) => {
        if (result.data) {
          this.alertS = true;
          this.message = 'Agendamento deletado com sucesso';

          let acompanhante = 0;
          if (this.agendamento.acompanhante != null) {
            acompanhante = 1;
          }
          this.ocupados = this.ocupados - 1 - acompanhante;
          this.vagos = this.capacidade - this.ocupados;

          this.listarAgendamentos();
          this.dialogConfirmDelete = false;
          this.limparAgendamento();
        }
      });
    },
    cancelar() {
      this.dialog = false;
      this.alterar = false;
      this.limparAgendamento();
      this.listarAgendamentos();
    },
  },
};

</script>
