<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Locais de consulta
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="65%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar local de consulta
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-alert
              v-model="alertError"
              color="error"
              type="error"
              dismissible
              dark
            >
              {{ errorMessage }}
            </v-alert>
            <v-layout wrap>
              <v-flex
                xs12
                md6
              >
                <v-text-field
                  v-model="local.nome"
                  v-validate="'required'"
                  data-vv-name="nome"
                  name="Nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                />
              </v-flex>
              <v-flex
                xs12
                md6
              >
                <v-select
                  v-model="local.localidade_id"
                  v-validate="'required'"
                  data-vv-name="localidade_id"
                  name="localidade_id"
                  label="* Localidade"
                  :items="localidades"
                  item-text="nome"
                  item-value="id"
                  :error-messages="errors.first('localidade_id')"
                />
              </v-flex>
              <v-flex
                xs12,
                md9
              >
                <v-text-field
                  v-model="local.endereco"
                  v-validate="'required'"
                  data-vv-name="endereco"
                  name="endereco"
                  label="* Endereço"
                  :error-messages="errors.first('endereco')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="local.numero"
                  v-validate="'required'"
                  data-vv-name="numero"
                  name="numero"
                  label="* Número"
                  :error-messages="errors.first('numero')"
                />
              </v-flex>
              <v-flex
                xs12
                md6
              >
                <v-text-field
                  v-model="local.bairro"
                  v-validate="'required'"
                  data-vv-name="bairro"
                  name="bairro"
                  label="* Bairro"
                  :error-messages="errors.first('bairro')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="local.telefone"
                  name="telefone"
                  label="Telefone"
                  :mask="mask_telefone"
                  :error-messages="errors.first('telefone')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="local.celular"
                  name="celular"
                  label="Celular"
                  :mask="mask_celular"
                  :error-messages="errors.first('celular')"
                />
              </v-flex>
              <v-flex xs12>
                <v-textarea
                  v-model="local.descricao"
                  name="Descricao"
                  label="Descrição"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar()"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar este Local?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="locais"
            :search="search"
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.localidade.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.localidade.estado }}
              </td>
              <td class="text-xs-left">
                {{ props.item.celular }}
              </td>
              <td class="text-xs-left">
                {{ props.item.telefone }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-local-de-consulta />
  </v-container>
</template>

<script>
import Local from '../../services/locaisDeConsulta';
import Localidade from '../../services/localidades';
import HelpLocalDeConsulta from './HelpLocaisDeConsulta.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpLocalDeConsulta,
  },
  data: () => ({
    alert: false,
    alertError: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    errorMessage: '',
    mask_telefone: '(##)####-####',
    mask_celular: '(##)#####-####',
    search: '',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'Localidade', value: 'localidade.nome' },
      { text: 'Estado', value: 'localidade.estado' },
      { text: 'Celular', value: 'celular' },
      { text: 'Telefone', value: 'telefone' },
    ],
    id: 0,
    local: {
      nome: '',
      localidade_id: '',
      bairro: '',
      endereco: '',
      numero: '',
      telefone: '',
      celular: '',
      descricao: '',
    },
    locais: [],
    localidades: [],
    dictionary: {
      custom: {
        nome: {
          required: 'O nome deve ser informado',
        },
        localidade_id: {
          required: 'A localidade do local de consulta deve ser informado',
        },
        bairro: {
          required: 'O bairro deve ser informado',
        },
        endereco: {
          required: 'O endereço deve ser informado',
        },
        numero: {
          required: 'O número deve ser informado',
        },
      },
    },
  }),
  mounted() {
    this.$validator.localize('en', this.dictionary);
    this.listarLocais();
    this.listarLocalidades();
  },
  methods: {
    listarLocais() {
      Local.listar().then((result) => {
        this.locais = result.data;
      });
    },
    listarLocalidades() {
      Localidade.listar().then((result) => {
        this.localidades = result.data;
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.local = {};
      this.errors.clear();
      this.$validator.reset();
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    salvar() {
      this.progress = true;

      this.$validator.validate().then((valid) => {
        if (valid) {
          if (this.id === 0) {
            Local.salvar(this.local).then((result) => {
              this.limpar();
              this.listarLocais();

              this.message = 'Local de consulta adicionado com sucesso';
              this.alert = true;
            }).catch((error) => {
              if (error.response.data.length > 0) {
                this.isErrors(error.response.data);
              }
            });
          } else {
            Local.alterar(this.local).then((result) => {
              this.limpar();
              this.listarLocais();

              this.message = 'Local de consulta alterado com sucesso';
              this.alert = true;
            }).catch((error) => {
              if (error.response.data.length > 0) {
                this.isErrors(error.response.data);
              }
            });
          }
        } else {
          this.errorMessage = 'Já existe um local cadastrado com estas informações';
        }
      });
    },
    editar(item) {
      this.id = item.id;
      this.local = item;
      this.dialog = true;
    },
    messageDelete(item) {
      this.local = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Local.deletar(this.local).then((result) => {
        this.alert = true;
        this.message = 'Local de consulta deletada com sucesso';

        this.listarLocais();
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
