<template>
  <v-container>
    <!-- VEICULO -->
    <v-alert
      v-model="alertWV"
      transition="scale-transition"
      origin="center center"
      color="warning"
      type="warning"
      dismissible
      dark
    >
      {{ messageWV }}
    </v-alert>
    <v-alert
      v-model="alertEV"
      transition="scale-transition"
      origin="center center"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ messageEV }}
    </v-alert>
    <!-- MOTORISTA -->
    <v-alert
      v-model="alertWM"
      transition="scale-transition"
      origin="center center"
      color="warning"
      type="warning"
      dismissible
      dark
    >
      {{ messageWM }}
    </v-alert>
    <v-alert
      v-model="alertEM"
      transition="scale-transition"
      origin="center center"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ messageEM }}
    </v-alert>
    <!-- SUCCESS -->
    <v-alert
      v-model="alertS"
      transition="scale-transition"
      origin="center center"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ messageS }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Organizar Viagens Fixas
      </v-toolbar-title>
    </v-toolbar>
    <v-card
      flat
      class="pa-3"
    >
      <v-container grid-list-md>
        <input
          ref="expInicio"
          v-model="parametro.expediente_inicio"
          name="expInicio"
          type="text"
          hidden
        >
        <v-layout wrap>
          <v-flex
            xs12
            md3
          >
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="horario"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="horario"
                  v-validate="`required|is_between:expInicio,${parametro.expediente_fim}`"
                  name="horario"
                  data-vv-name="horario"
                  label="* Horário"
                  prepend-icon="access_time"
                  :error-messages="errors.first('horario')"
                  readonly
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-if="menu2"
                v-model="horario"
                format="24hr"
                data-vv-name="horario"
                full-width
                @click:minute="$refs.menu.save(horario)"
              />
            </v-menu>
          </v-flex>
          <v-flex
            xs12
            md3
          >
            <v-menu
              v-model="menuI"
              lazy
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                ref="dataInicial"
                v-model="dataInicialFormated"
                v-validate="'required|date_format:DD/MM/YYYY'"
                data-vv-name="dataInicial"
                :error-messages="errors.first('dataInicial')"
                prepend-icon="event"
                label="* Inicio"
                readonly
              />
              <v-date-picker
                v-model="dataInicial"
                locale="pt-BR"
                data-vv-name="dataInicial"
                @input="menuI = false"
              />
            </v-menu>
          </v-flex>
          <v-flex
            xs12
            md3
          >
            <v-menu
              v-model="menuF"
              lazy
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="dataFinalFormated"
                v-validate="'required|after:dataInicial|date_format:DD/MM/YYYY'"
                data-vv-name="dataFinal"
                :error-messages="errors.first('dataFinal')"
                prepend-icon="event"
                label="* Final"
                readonly
              />
              <v-date-picker
                v-model="dataFinal"
                locale="pt-BR"
                data-vv-name="dataFinal"
                @input="menuF = false"
              />
            </v-menu>
          </v-flex>
          <v-flex xs12>
            <v-select
              v-model="dsSelecionados"
              v-validate="'required'"
              name="dsSelecionados"
              label="Dias da Semana"
              data-vv-name="dsSelecionados"
              :error-messages="errors.first('dsSelecionados')"
              :items="diasSemana"
              chips
              multiple
            >
              <template
                slot="selection"
                slot-scope="data"
              >
                <v-chip
                  :selected="data.selected"
                  close
                  @input="remove(data.item)"
                >
                  <strong>{{ data.item }}</strong>&nbsp;
                </v-chip>
              </template>
            </v-select>
          </v-flex>
          <v-flex
            xs12
            sm6
            md6
          >
            <v-autocomplete
              v-model="localidade_id"
              v-validate="'required'"
              name="localidade_id"
              label="* Localidade"
              data-vv-name="localidade_id"
              :error-messages="errors.first('localidade_id')"
              :items="localidades"
              item-text="nome"
              item-value="id"
            />
          </v-flex>
          <v-flex
            xs12
            sm4
            md6
          >
            <v-autocomplete
              v-model="veiculo_id"
              v-validate="'required'"
              name="veiculo_id"
              label="* Veículo"
              data-vv-name="veiculo_id"
              :error-messages="errors.first('veiculo_id')"
              :items="veiculos"
              item-text="numModCap"
              item-value="id"
              hint="número - modelo - capacidade"
            />
          </v-flex>
          <v-flex xs12>
            <v-autocomplete
              v-model="motorista_id"
              v-validate="'required'"
              name="motorista_id"
              label="* Motorista"
              data-vv-name="motorista_id"
              :error-messages="errors.first('motorista_id')"
              :items="motoristas"
              item-text="nome"
              item-value="id"
            />
          </v-flex>
          <v-flex xs12>
            <v-expansion-panel
              v-model="panel"
              expand
            >
              <v-expansion-panel-content>
                <div slot="header">
                  Agendamentos
                </div>
                <v-container>
                  <v-data-table
                    v-model="viagensSelecionadas"
                    :headers="headers"
                    :items="listaViagens"
                    item-key="data"
                    select-all
                  >
                    <template v-slot:items="props">
                      <td>
                        <v-checkbox
                          v-model="props.selected"
                          primary
                          hide-details
                        />
                      </td>
                      <td class="text-xs-left">
                        {{ moment(props.item.data).format('DD/MM/YYYY') }}
                      </td>
                      <td class="text-xs-left">
                        {{ diasSemana[moment(props.item.data).weekday()-1] }}
                      </td>
                      <td class="justify-center layout px-0">
                        <v-icon
                          small
                          @click="removeViagem(props.item)"
                        >
                          delete
                        </v-icon>
                      </td>
                    </template>
                  </v-data-table>
                </v-container>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-flex>
          <v-flex xs12>
            <v-btn
              color="grey darken-1"
              flat
              outline
              @click="gerarViagensPeriodo"
            >
              Gerar viagens
            </v-btn>
            <v-btn
              color="blue darken-1"
              flat
              outline
              @click="salvar"
            >
              Salvar
            </v-btn>
            <v-btn
              color="red darken-1"
              flat
              outline
              @click="cancelar"
            >
              Cancelar
            </v-btn>
            <v-btn
              color="grey darken-1"
              outline
              dark
              :to="'/viagens'"
            >
              Voltar
            </v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
    <help-fixa />
  </v-container>
</template>

<script>
import { Validator } from 'vee-validate';

import { log } from 'util';
import Motorista from '../../services/motoristas';
import Veiculo from '../../services/veiculos';
import Localidade from '../../services/localidades';
import Viagem from '../../services/viagens';
import Parametrizacao from '../../services/parametrizacao';
import HelpFixa from './HelpFixa.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpFixa,
  },
  data: () => ({
    panel: [true],
    menu2: false,
    progress: false,
    validEM: true,
    menuI: false,
    menuF: false,
    alertS: false,
    alertEV: false,
    alertWV: false,
    alertEM: false,
    alertWM: false,
    messageS: '',
    messageEV: '',
    messageWV: '',
    messageEM: '',
    messageWM: '',
    headers: [
      { text: 'Data', value: 'data' },
      { text: 'Dia da semana', value: 'dia_da_semana' },
    ],
    horaAtual: null,
    parametro: {
      expediente_inicio: null,
      expediente_fim: null,
    },
    dataInicial: null,
    dataFinal: null,
    horario: null,
    localidade_id: null,
    veiculo_id: null,
    motorista_id: null,
    viagem: {
      data: null,
      horario: null,
      localidade_id: null,
      veiculo_id: null,
      motorista_id: null,
      status: 'Agendada',
      tipo: 'Fixa',
    },
    listaViagens: [],
    viagensSelecionadas: [],
    veiculo: {
      id: null,
      numModCap: '',
    },
    veiculos: [],
    localidades: [],
    motoristas: [],
    diasSemana: [
      'Segunda-Feira',
      'Terça-Feira',
      'Quarta-Feira',
      'Quinta-Feira',
      'Sexta-Feira',
      'Sabado',
    ],
    dsSelecionados: [],
    dictionary: {
      custom: {
        dataInicial: {
          required: 'A data inicial deve ser informada',
        },
        dataFinal: {
          required: 'A data final deve ser informada',
          after: 'Data final tem que ser maior que a data inicial',
        },
        horario: {
          is_between: 'Horário deve estar entre o inicio do expediente e o fim do expediente',
          required: 'O horário deve ser informado',
          min: 'O horário não foi informado corretamente',
        },
        localidade_id: {
          required: 'A localidade deve ser informado',
        },
        veiculo_id: {
          required: 'O veículo deve ser informado',
        },
        motorista_id: {
          required: 'O motorista deve ser informado',
        },
        dsSelecionados: { required: 'Informe os dias da semana' },
      },
    },
  }),
  computed: {
    dataInicialFormated() {
      return this.dataInicial != null ? this.moment(this.dataInicial).format('DD/MM/YYYY') : null;
    },
    dataFinalFormated() {
      return this.dataFinal != null ? this.moment(this.dataFinal).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    dsSelecionados() {
      if (this.listaViagens.length > 0) {
        this.gerarViagensPeriodo();
      }
    },
    motorista_id() {
      this.buscarPorMotoristaPeriodo();
    },
    veiculo_id() {
      this.buscarPorVeiculoPeriodo();
    },
    horario() {
      if (this.horario.length > 4) {
        this.buscarPorVeiculoPeriodo();
        this.buscarPorMotoristaPeriodo();
      }
    },
    dataInicial() {
      this.buscarPorVeiculoPeriodo();
      this.buscarPorMotoristaPeriodo();
      if (this.listaViagens.length > 0) {
        this.gerarViagensPeriodo();
      }
    },
    dataFinal() {
      this.buscarPorVeiculoPeriodo();
      this.buscarPorMotoristaPeriodo();
      if (this.listaViagens.length > 0) {
        this.gerarViagensPeriodo();
      }
    },
  },
  created() {
    Validator.extend('is_between', (value, [valueIni, valueFim]) => this.moment(value, 'HH:mm').isBetween(this.moment(valueIni, 'HH:mm'), this.moment(valueFim, 'HH:mm')), {
      hasTarget: true,
    });
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);

    Parametrizacao.show(0).then((result) => {
      this.parametro.expediente_inicio = result.data[0].expediente_inicio;
      this.parametro.expediente_fim = result.data[0].expediente_fim;
    });

    this.listarMotoristas();
    this.listarVeiculos();
    this.listarLocalidades();
  },
  methods: {
    remove(item) {
      this.dsSelecionados.splice(this.dsSelecionados.indexOf(item), 1);
      this.dsSelecionados = [...this.dsSelecionados];
    },
    removeViagem(item) {
      this.listaViagens.splice(this.listaViagens.indexOf(item), 1);
      this.listaViagens = [...this.listaViagens];
    },
    listarMotoristas() {
      Motorista.listar().then((result) => {
        this.motoristas = result.data;
      });
    },
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        result.data.forEach((item) => {
          if (item.bloqueio_ativo != 'Sucateado'
            && item.bloqueio_ativo != 'Manutenção'
            && item.bloqueio_ativo != 'Venda') {
            this.veiculo = {
              id: item.id,
              numModCap: `${item.numero} - ${item.modelo} - ${item.capacidade}`,
            };
            this.veiculos.push(this.veiculo);
          }
        });
      });
    },
    listarLocalidades() {
      Localidade.listar().then((result) => {
        this.localidades = result.data;
      });
    },
    buscarPorVeiculoPeriodo() {
      if (this.veiculo_id != null) {
        if (this.horario != null) {
          if (this.dataInicial != null) {
            if (this.dataFinal != null) {
              this.alertE = false;
              Viagem.buscarPorVeiculoPeriodo(this.dataInicial, this.dataFinal, this.veiculo_id)
                .then((result) => {
                  if (result.data.length > 0) {
                    const itens = result.data.filter(item => !this.moment(this.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                      && !this.moment(this.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
                    if (itens.length > 0) {
                      this.listaViagens = [];
                      this.validEM = false; this.alertWV = false; this.alertEV = true;
                      this.messageEV = 'Existem viagens para o veículo no periodo informado';
                    } else {
                      this.validEM = true;
                      this.alertEV = false;
                      this.alertWV = true;
                      this.messageWV = 'Existem viagens para o veículo no periodo informado, porém o horário informado difere de 6 horas do horário dessas viagens';
                    }
                  } else {
                    this.validEM = true;
                    this.alertEV = false;
                    this.alertWV = false;
                  }
                });
            } else { this.errors.add({ field: 'dataFinal', msg: 'A data final deve ser informada' }); }
          } else { this.errors.add({ field: 'dataInicial', msg: 'A data inicial deve ser informada' }); }
        } else { this.errors.add({ field: 'horario', msg: 'O horário deve ser informado' }); }
      }
    },
    buscarPorMotoristaPeriodo() {
      if (this.motorista_id != null) {
        if (this.horario != null) {
          if (this.dataInicial != null) {
            if (this.dataFinal != null) {
              this.alertE = false;
              Viagem
                .buscarPorMotoristaPeriodo(this.dataInicial, this.dataFinal, this.motorista_id)
                .then((result) => {
                  if (result.data.length > 0) {
                    const itens = result.data.filter(item => !this.moment(this.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                      && !this.moment(this.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
                    if (itens.length > 0) {
                      this.listaViagens = [];
                      this.validEM = false; this.alertWM = false; this.alertEM = true;
                      this.messageEM = 'Existem viagens para o motorista no periodo informado';
                    } else {
                      this.validEM = true;
                      this.alertEM = false;
                      this.alertWM = true;
                      this.messageWM = 'Existem viagens para o motorista no periodo informado, porém o horário informado difere de 6 horas do horário dessas viagens';
                    }
                  } else {
                    this.validEM = true;
                    this.alertEM = false;
                    this.alertWM = false;
                  }
                });
            } else { this.errors.add({ field: 'dataFinal', msg: 'A data final deve ser informada' }); }
          } else { this.errors.add({ field: 'dataInicial', msg: 'A data inicial deve ser informada' }); }
        } else { this.errors.add({ field: 'horario', msg: 'O horário deve ser informado' }); }
      }
    },
    gerarViagensPeriodo() {
      this.$validator.validate().then((valid) => {
        if (this.moment(this.dataInicialFormated).isBefore(this.moment(this.moment(new Date()).format('DD/MM/YYYY')).add(1, 'days'))) {
          this.errors.add({
            field: 'dataInicial',
            msg: 'Data inicial tem que ser posterior a data atual',
          });
          valid(false);
        }

        if (this.moment(this.dataInicial, 'YYYY-MM-DD').format('YYYY-MM-DD') == this.moment(new Date(), 'YYYY-MM-DD').format('YYYY-MM-DD')) {
          if (this.moment(this.horario, 'HH:mm').isBefore(this.moment(new Date(), 'HH:mm'))) {
            this.errors.add({
              field: 'horario',
              msg: 'Horário deve ser posterior ao horário atual',
            });
          }
        }

        this.buscarPorVeiculoPeriodo();
        this.buscarPorMotoristaPeriodo();
        if (this.validEM) {
          if (valid) {
            this.listaViagens = [];
            const dtFinal = this.moment(this.moment(this.dataFinal).add(1, 'days')).format('YYYY-MM-DD');
            for (let data = this.dataInicial; this.moment(data).isBefore(dtFinal); data = this.moment(data).add(1, 'days')) {
              const diaSemana = this.diasSemana[this.moment(data).weekday() - 1];

              if (this.dsSelecionados.indexOf(diaSemana) !== -1) {
                this.viagem = {
                  data: this.moment(data).format('YYYY-MM-DD'),
                  horario: this.horario,
                  localidade_id: this.localidade_id,
                  veiculo_id: this.veiculo_id,
                  motorista_id: this.motorista_id,
                  status: 'Agendada',
                  tipo: 'Fixa',
                };
                this.listaViagens.push(this.viagem);
              }
            }
            this.progress = false;
          } else this.progress = false;
        }
      });
    },
    limpar() {
      this.$validator.reset();

      this.progress = false;
      this.alertEV = false;
      this.alertS = false;
      this.alertWV = false;
      this.alertEM = false;
      this.alertWM = false;

      this.messagesE = [];
      this.messagesW = [];

      this.horario = '';
      this.dataInicial = null;
      this.dataFinal = null;
      this.dsSelecionados = [];
      this.localidade_id = null;
      this.veiculo_id = null;
      this.motorista_id = null;
      this.listaViagens = [];
    },
    salvar() {
      this.progress = true;
      if (this.listaViagens.length > 0) {
        this.$validator.validate().then((valid) => {
          if (this.moment(this.dataInicialFormated).isBefore(this.moment(this.moment(new Date()).format('DD/MM/YYYY')).add(1, 'days'))) {
            this.errors.add({
              field: 'dataInicial',
              msg: 'Data inicial tem que ser posterior a data atual',
            });
            valid(false);
          }

          if (this.moment(this.dataInicial, 'YYYY-MM-DD').format('YYYY-MM-DD') == this.moment(new Date(), 'YYYY-MM-DD').format('YYYY-MM-DD')) {
            if (this.moment(this.horario, 'HH:mm').isBefore(this.moment(new Date(), 'HH:mm'))) {
              this.errors.add({
                field: 'horario',
                msg: 'Horário deve ser posterior ao horário atual',
              });
            }
          }
          this.buscarPorVeiculoPeriodo();
          this.buscarPorMotoristaPeriodo();
          if (this.validEM) {
            if (valid) {
              this.dataInicial = this.moment(this.dataInicial).format('YYYY-MM-DD');
              this.dataFinal = this.moment(this.dataFinal).format('YYYY-MM-DD');

              this.listaViagens.forEach((item) => {
                Viagem.salvar(item).then((result) => { }).catch((err) => {
                  this.alertE = true;
                  this.messagesE = `Error:: ${err}`;
                });
              });
              this.limpar();
              this.messageS = 'Viagens adicionadas com sucesso';
              this.alertS = true;
            }
          }
        });
      } else {
        this.alertEV = true;
        this.messageEV = 'Não foi gerada nenhuma viagem ainda';
      }
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
