        text: 'Agendamento de pacientes',
        model: false,
        children: [
          { icon: 'list', text: 'Todos', route: '/agendamentos/todos' },
          { icon: 'person_add', text: 'Agendar paciente', route: '/agendamentos/agendar-pacientes' },
        ],
      },
      {
        icon: 'history ',
        'icon-alt': 'history',
        text: 'Viagens',
        model: false,
        children: [
          { icon: 'list', text: 'Listar', route: '/viagens' },
          { icon: 'history', text: 'Fixa', route: '/viagens/fixa' },
          { icon: 'report', text: 'Emergencia', route: '/viagens/emergencia' },
        ],
      },
      { icon: 'arrow_forward', text: 'Saida de Veículos', route: '/saida-veiculos' },
      { icon: 'arrow_back', text: 'Retorno de Veículos', route: '/retorno-veiculos' },
      { icon: 'block', text: 'Bloqueio de Veículos', route: '/bloqueio-veiculos' },
      { icon: 'person_outline', text: 'Pacientes', route: '/pacientes' },
      { icon: 'commute', text: 'Veículos', route: '/veiculos' },
