          { icon: '', text: 'Cid 10', route: '/cids' },
        ],
      },
    ],
  }),
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    },
    getTitle() {
      return this.$store.getters.getTitle;
    },
  },
  mounted() {
    this.$store.commit('updatedToken', localStorage.getItem('token'));
    this.$store.commit('updatedUser', localStorage.getItem('usuario'));
  },
  methods: {
    logout() {
      localStorage.removeItem('token');
      localStorage.removeItem('usuario');
      this.$store.commit('updatedToken', null);
      this.$store.commit('updatedUser', null);
      this.$router.replace('/login');
    },

  },
};
</script>
