<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Pacientes
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="50%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Pacientes
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="paciente.nome"
                  name="nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.cpf"
                  name="cpf"
                  label="CPF"
                  :mask="mask_cpf"
                  :error-messages="errors.first('cpf')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.rg"
                  name="rg"
                  label="RG"
                  :mask="mask_rg"
                  :error-messages="errors.first('rg')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.sus"
                  name="sus"
                  label="SUS"
                  :error-messages="errors.first('sus')"
                />
              </v-flex>
              <v-flex
                xs12
                md10
              >
                <v-text-field
                  v-model="paciente.endereco"
                  name="endereco"
                  label="* Endereço"
                  :error-messages="errors.first('endereco')"
                />
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="paciente.numero"
                  name="numero"
                  label="* Número"
                  :error-messages="errors.first('numero')"
                />
              </v-flex>
              <v-flex
                xs12
                md9
              >
                <v-text-field
                  v-model="paciente.bairro"
                  name="bairro"
                  label="* Bairro"
                  :error-messages="errors.first('bairro')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.cep"
                  name="cep"
                  label="* Cep"
                  :mask="mask_cep"
                  :error-messages="errors.first('cep')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.celular"
                  name="celular"
                  label="* Celular"
                  :mask="mask_celular"
                  :error-messages="errors.first('celular')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.celular_contato"
                  name="celular_contato"
                  label="Celular para contato"
                  :mask="mask_celular"
                  :error-messages="errors.first('celular_contato')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.telefone"
                  name="telefone"
                  label="Telefone"
                  :mask="mask_telefone"
                  :error-messages="errors.first('telefone')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="paciente.telefone_contato"
                  name="telefone_contato"
                  label="Telefome para contato"
                  :mask="mask_telefone"
                  :error-messages="errors.first('telefone_contato')"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              >
                Comfirma?
              </v-card-title>
              <v-card-text>
                Você deseja realmente deletar este Paciente?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="pacientes"
            :search="search"
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.celular }}
              </td>
              <td class="text-xs-left">
                {{ props.item.endereco }}, {{ props.item.numero }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-paciente />
  </v-container>
</template>

<script>
import Paciente from '../../services/pacientes';
import HelpPaciente from './HelpPaciente.vue';

export default {
  components: {
    HelpPaciente,
  },
  data: () => ({
    alert: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    search: '',
    mask_cpf: '###.###.###-##',
    mask_rg: '########N',
    mask_cep: '#####-###',
    mask_telefone: '(##)####-####',
    mask_celular: '(##)#####-####',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'Celular', value: 'celular' },
      { text: 'Endereço', value: 'endereço' },
    ],
    id: 0,
    paciente: {
      nome: '',
      cpf: '',
      rg: '',
      sus: '',
      bairro: '',
      endereco: '',
      numero: '',
      cep: '19640000',
      celular: '',
      celular_contato: '',
      telefone: '',
      telefone_contato: '',
    },
    pacientes: [],
  }),
  mounted() {
    this.listarPacientes();
  },
  methods: {
    listarPacientes() {
      Paciente.listar().then((result) => {
        this.pacientes = result.data;
      });
    },
    addError(field, msg) {
      this.errors.add({ field, msg });
    },
    isErrors(listErrors = null) {
      this.errors.clear();

      this.progress = false;

      listErrors.forEach((err) => {
        this.addError(err.field, err.message);
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.paciente = {};
      this.errors.clear();
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Paciente.salvar(this.paciente).then((result) => {
          this.limpar();
          this.listarPacientes();

          this.message = 'Paciente adicionado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      } else {
        Paciente.alterar(this.paciente).then((result) => {
          this.limpar();
          this.listarPacientes();

          this.message = 'Paciente alterado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;
      this.paciente = item;
      this.dialog = true;
    },
    messageDelete(item) {
      this.paciente = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Paciente.deletar(this.paciente).then((result) => {
        this.listarPacientes();

        this.message = 'Paciente deletado com sucesso';
        this.alert = true;
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
