import { http } from './config';

export default {
  salvar: cid => http.post('cids', cid),
  alterar: cid => http.put(`cids/${cid.id}`, cid),
  deletar: cid => http.delete(`cids/${cid.id}`),
  listar: () => http.get('cids'),
  listarPorEspecialidade: especialidade => http.get(`especialidades/${especialidade}/cids`),
};
