import { http } from './config';

export default {
  salvar: agendamento => http.post('agendamentos', agendamento),
  alterar: agendamento => http.put(`agendamentos/${agendamento.id}`, agendamento),
  deletar: agendamento => http.delete(`agendamentos/${agendamento.id}`),
  show: id => http.get(`agendamentos/${id}`),
  buscaPorViagem: id => http.get(`viagens/${id}/agendamentos`),
  buscaPorPaciente: (pacienteId, data, agendamentoId) => http.get(`pacientes/${pacienteId}/agendamentos`, {
    params: { data, agendamentoId },
  }),
  listar: () => http.get('agendamentos'),
};
