<template>
  <v-container>
    <v-alert
      v-model="alert"
      type="error"
      class="error"
      dismissible
      :value="true"
      fade-transition
    >
      {{ message }}
    </v-alert>
    <template>
      <v-toolbar
        flat
        color="green lighten-4"
      >
        <v-toolbar-title class="font-weight-light title text-uppercase">
          Relatórios
        </v-toolbar-title>
        <v-spacer />
      </v-toolbar>
      <v-card flat>
        <v-container grid-list-md>
          <v-layout
            row
            wrap
          >
            <v-flex
              xs12
              md8
            >
              <v-select
                v-model="optionSelected"
                :items="items"
                label="Opção de relatório"
                item-text="name"
                item-value="id"
              />
            </v-flex>
            <v-flex
              v-if="optionSelected == 1"
              xs12
              md4
            >
              <v-menu
                v-model="menuData"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="dataViagemFormatted"
                  v-validate="'required|date_format:DD/MM/YYYY'"
                  data-vv-name="dataViagem"
                  name="dataViagem"
                  :error-messages="errors.first('dataViagem')"
                  label="* Data"
                  prepend-icon="event"
                  readonly
                />
                <v-date-picker
                  v-model="dataViagem"
                  locale="pt-BR"
                  data-vv-name="dataViagem"
                  @input="menuData = false"
                />
              </v-menu>
            </v-flex>
            <template v-if="optionSelected == 1">
              <v-flex xs12>
                <v-autocomplete
                  v-model="viagem_id"
                  v-validate="'required'"
                  :items="listaViagens"
                  name="viagem_id"
                  data-vv-name="viagem_id"
                  box
                  chips
                  color="blue-grey lighten-2"
                  label="* Viagens"
                  item-text="datHorLoc"
                  item-value="id"
                  :error-messages="errors.first('viagem_id')"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      :selected="data.selected"
                      close
                      class="chip--select-multi"
                      @input="remove(data.item)"
                    >
                      {{ data.item.data }} -
                      {{ data.item.horario }} -
                      {{ data.item.localidade.nome }}
                    </v-chip>
                  </template>
                  <template v-slot:item="data">
                    <template v-if="typeof data.item !== 'object'">
                      <v-list-tile-content v-text="data.item" />
                    </template>
                    <template v-else>
                      <v-list-tile-avatar v-if="data.item.tipo == 'Fixa'">
                        <v-icon>
                          history
                        </v-icon>
                      </v-list-tile-avatar>
                      <v-list-tile-avatar v-if="data.item.tipo == 'Emergencia'">
                        <v-icon>
                          report
                        </v-icon>
                      </v-list-tile-avatar>
                      <v-list-tile-content>
                        <v-list-tile-sub-title v-html="`<b>Data:</b> ${data.item.data}  <b>Horário:</b> ${data.item.horario}  <b>Localidade:</b> ${data.item.localidade.nome}`" />
                        <v-list-tile-sub-title v-html="`<b>Motorista:</b> ${data.item.motorista.nome}`" />
                      </v-list-tile-content>
                    </template>
                  </template>
                </v-autocomplete>
              </v-flex>
            </template>

            <!-- opcao 2 -->
            <template v-if="optionSelected == 2">
              <v-flex
                xs12
                md2
              >
                <v-menu
                  v-model="menuDataIni"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="dataIniFormatted"
                    v-validate="'required|date_format:DD/MM/YYYY'"
                    data-vv-name="dataIni"
                    name="dataIni"
                    :error-messages="errors.first('dataIni')"
                    label="* Data"
                    prepend-icon="event"
                    readonly
                  />
                  <v-date-picker
                    v-model="dataIni"
                    locale="pt-BR"
                    data-vv-name="dataIni"
                    @input="menuDataIni = false"
                  />
                </v-menu>
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-menu
                  v-model="menuDataFin"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="dataFinFormatted"
                    v-validate="'required|date_format:DD/MM/YYYY'"
                    data-vv-name="dataFin"
                    name="dataFin"
                    :error-messages="errors.first('dataFin')"
                    label="* Data Final"
                    prepend-icon="event"
                    readonly
                  />
                  <v-date-picker
                    v-model="dataFin"
                    locale="pt-BR"
                    data-vv-name="dataFin"
                    @input="menuDataFin = false"
                  />
                </v-menu>
              </v-flex>
            </template>
            <v-flex
              v-if="optionSelected"
              xs12
              md3
            >
              <v-btn
                color="#111"
                outline
                @click="gerarRelatorio()"
              >
                <v-icon>settings</v-icon>Abrir
              </v-btn>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </template>
    <template>
      <rel-viagem-motorista
        v-if="activeReport && optionSelected == 1"
        :dialog="dialog"
        :viagem="viagem"
        @changeDialog="dialog = $event"
      />
      <rel-viagens
        v-if="activeReport && optionSelected == 2"
        :dialog="dialog"
        :viagens="viagens"
        @changeDialog="dialog = $event"
      />
    </template>
  </v-container>
</template>

<script>
import RelViagemMotorista from '../../components/Relatorio/RelatViagemMotorista.vue';
import RelViagens from '../../components/Relatorio/RelatViagens.vue';
import Viagem from '../../services/viagens';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    RelViagemMotorista,
    RelViagens,
  },
  data: () => ({
    activeReport: false,
    dialog: false,
    menuData: false,
    menuDataIni: false,
    menuDataFin: false,
    alert: false,
    message: '',
    dataViagem: null,
    dataIni: null,
    dataFin: null,
    viagem_id: null,
    viagem: {
      data: null,
      horario: null,
      localidade: { nome: null },
      motorista: { nome: '' },
      veiculo_id: null,
      tipo: null,
      status: null,
      agendamentos: [],
    },
    optionSelected: null,
    listaViagens: [],
    items: [
      { id: 1, name: 'Relatório da viagem para o motorista' },
      { id: 2, name: 'Relatório de todas viagens' },
    ],
    viagens: [],
    dictionary: {
      custom: {
        viagem_id: {
          required: 'Selecione a viagem para gerar o relatório',
        },
      },
    },
  }),
  computed: {
    dataViagemFormatted() {
      return this.moment(this.dataViagem).format('DD/MM/YYYY');
    },
    dataIniFormatted() {
      return this.dataIni ? this.moment(this.dataIni).format('DD/MM/YYYY') : null;
    },
    dataFinFormatted() {
      return this.dataFin ? this.moment(this.dataFin).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    dataViagem() {
      if (this.dataViagem) {
        this.listarViagens();
      }
    },
    optionSelected() {
      if (this.optionSelected === 1) {
        this.listarViagens();
      }
    },
    viagem_id() {
      if (this.viagem_id) {
        Viagem.show(this.viagem_id).then((result) => {
          this.viagem = result.data;
        });
      }
    },
    dataIni() {
      if (this.dataFin) {
        this.listarTodasViagensPeriodo();
      }
    },
    dataFin() {
      if (this.dataIni) {
        this.listarTodasViagensPeriodo();
      }
    },
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);
    const dataAtual = new Date().toISOString().substr(0, 10);

    this.dataViagem = dataAtual;
  },
  methods: {
    remove(item) {
      this.viagem = null;
      this.viagem_id = null;
    },
    listarTodasViagensPeriodo() {
      Viagem.buscarPorPeriodo(this.dataIni, this.dataFin).then((res) => {
        this.viagens = res.data;
      });
    },
    listarViagens() {
      this.listaViagens = [];

      Viagem.buscarPorVeiculoPeriodo(this.dataViagem)
        .then((result) => {
          result.data.forEach((item) => {
            this.viagem = item;
            this.viagem.data = this.moment(item.data).format('DD/MM/YYYY');
            this.listaViagens.push(this.viagem);
          });
        });
    },
    gerarRelatorio() {
      if (this.optionSelected === 1) {
        if (this.viagem_id) {
          this.alert = false;
          this.dialog = true;
          this.activeReport = true;
        } else {
          this.alert = true;
          this.message = 'Não foi selecionado nenhum dado para gerar relatório';
        }
      }
      if (this.optionSelected === 2) {
        this.alert = false;
        this.dialog = true;
        this.activeReport = true;
      }
    },
  },
};
</script>
