                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md4
              >
                <v-text-field
                  v-model="bloqueio.data_baixa"
                  label="Data da baixa"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md4
              >
                <v-text-field
                  v-model="bloqueio.numero_baixa"
