            </v-layout>
            <br>
            <v-flex xs12>
              <v-btn
                v-if="bloqueio.motivo == 'Manutenção' && bloqueio.data_retorno == null"
                color="primary"
                outline
                flat
                @click="desbloqueio(bloqueio)"
              >
                Desbloquear
              </v-btn>
              <v-btn
                v-if="bloqueio.data_retorno == null"
