<script>
import { Validator } from 'vee-validate';
import Bloqueio from '../../services/bloqueios';
import Veiculo from '../../services/veiculos';
import Viagem from '../../services/viagens';

import HelpBloqueio from './HelpBloqueio.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpBloqueio,
  },
  data: () => ({
    progress: false,
    alertE: false,
    alertS: false,
    messageS: '',
    messageE: '',
    menuDataBloqueio: false,
    menuDataPrevRetorno: false,
    menuDataBaixa: false,
    menuDataRetorno: false,
    message: '',
    dialogBloqueio: false,
    dialogDesbloqueio: false,
    dialogConfirmDelete: false,
    dialogDetalhesBloqueio: false,
    search: '',
    headers: [
      { text: 'Número', value: 'número' },
      { text: 'Modelo', value: 'modelo' },
      { text: 'Marca', value: 'marca' },
      { text: 'Motivo', value: 'motivo' },
    ],
    motivos: ['Manutenção', 'Sucateado', 'Venda'],
    veiculo: {
      id: null,
      marca: '',
      modelo: '',
      numero: '',
      placa: '',
      capacidade: null,
      ano: null,
      bloqueio_ativo: null,
      numNome: null,
      quilometragem_atual: 0,
      quilometragem_anterior: 0,
    },
    veiculosSem: [],
    bloqueios: [],
    id: 0,
    bloqueio: {
      veiculo_id: null,
      motivo: 'Manutenção',
      observacoes: null,
      quilometragem_baixa: null,
      numero_baixa: null,
      data_baixa: null,
      data_de_bloqueio: null,
      data_previsao_retorno: null,
      data_retorno: null,
      quilometragem_atual: null,
      quilometragem_saida: null,
      quilometragem_retorno: null,
    },
    dictionary: {
      custom: {
        veiculo_id: {
          required: 'O veículo não foi informado',
          is_agendamentos: 'Veiculo possui viagens agendadas',
        },
        motivo: {
          required: 'O motivo de bloqueio deve ser informado',
        },
        data_de_bloqueio: {
          required: 'A data em que foi realizado o bloqueio deve ser informada',
          is_before_actual: 'A data de bloqueio deve ser inferior ou igual a data atual',
        },
        data_baixa: {
          required: 'A data em que foi realizado o bloqueio deve ser informada',
          is_before_actual: 'A data da baixa deve ser inferior ou igual a data atual',
        },
        data_previsao_retorno: {
          required: 'A data da previsão de retorno deve ser informada',
          is_after: 'A data de previsão de retorno deve ser igual ou posterior a data de bloqueio',
        },
        numero_baixa: {
          required: 'O número da baixa deve ser informado',
        },
        quilometragem_atual: {
          required: 'A quilometragem do veículo deve ser informada',
        },
      },
    },
  }),
  computed: {
    dataBloqueioFormatted() {
      return this.bloqueio.data_de_bloqueio != null ? this.moment(this.bloqueio.data_de_bloqueio).format('DD/MM/YYYY') : null;
    },
    dataPrevisaoRetornoFormatted() {
      return this.bloqueio.data_previsao_retorno != null ? this.moment(this.bloqueio.data_previsao_retorno).format('DD/MM/YYYY') : null;
    },
    dataRetornoFormatted() {
      return this.bloqueio.data_retorno != null ? this.moment(this.bloqueio.data_retorno).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'bloqueio.motivo': function () {
      this.$validator.reset();
    },
    dialogBloqueio() {
      if (this.dialogBloqueio) {
        this.limpar();
        this.bloqueio.motivo = 'Manutenção';
      }
    },
    // eslint-disable-next-line func-names
    'bloqueio.veiculo_id': function () {
      Veiculo.show(this.bloqueio.veiculo_id).then((result) => {
        this.veiculo = result.data;
      });
    },
  },
  created() {
    Validator.extend('is_after', (value, [othervalue]) => this.moment(value, 'DD/MM/YYYY')
      .isAfter(this.moment(this.moment(othervalue, 'DD/MM/YYYY').subtract(1, 'days'), 'DD/MM/YYYY')), { hasTarget: true });

    Validator.extend('is_before', (value, [bloqueio]) => {
      const retorno = this.moment(value).format('DD/MM/YYYY');
      return !this.moment(retorno, 'DD/MM/YYYY').isBefore(this.moment(bloqueio, 'DD/MM/YYYY'));
    }, {
      hasTarget: true,
    });

    Validator.extend('is_before_actual', value => this.moment(value, 'DD/MM/YYYY').isBefore(this.moment(this.moment(this.moment().add(1, 'days')).format('DD/MM/YYYY'), 'DD/MM/YYYY')),
      { hasTarget: true });

    Validator.extend('is_agendamentos', value => (
      Viagem.buscarPorVeiculoPeriodo(null, null, value, null, 0).then((result) => {
        if (result.data.length > 0) { return false; }
        return true;
      })
    ), { hasTarget: true });
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);
    this.dataAtual = this.moment(new Date()).format('DD/MM/YYYY');

    this.listarVeiculos();
    this.listarBloqueios();
  },
  methods: {
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        result.data.forEach((item) => {
          if (item.bloqueio_ativo !== 'Sucateado' && item.bloqueio_ativo !== 'Venda') {
            this.veiculo = {
              id: item.id,
              numNome: `${item.numero} - ${item.modelo}`,
              bloqueio_ativo: item.bloqueio_ativo,
            };

            this.veiculosSem.push(this.veiculo);
          }
        });
      });
    },
    listarBloqueios() {
      Bloqueio.listar().then((result) => {
        this.bloqueios = result.data;
      });
    },
    addErrors(field, msg) {
      this.errors.add({
        field,
        msg,
      });
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.addErrors(err.field, err.message);
      });
    },
    limpar() {
      this.$validator.reset();

      this.dialogConfirmDelete = false;
      this.progress = false;
      this.alertE = false;
      this.alertS = false;

      this.bloqueio = {
        veiculo_id: null,
        observacoes: null,
        quilometragem_baixa: null,
        numero_baixa: null,
        data_baixa: null,
        data_de_bloqueio: null,
        data_previsao_retorno: null,
        data_retorno: null,
        quilometragem_atual: null,
        quilometragem_saida: null,
        quilometragem_retorno: null,
      };
    },
    salvarDesbloqueio() {
      this.errors.clear();
      let valid = true;

      if (this.bloqueio.data_retorno == null) {
        this.addErrors('data_retorno', 'A data de retorno deve ser informada');
        valid = false;
      } else if (this.moment(this.dataRetornoFormatted, 'DD/MM/YYYY').isBefore(this.moment(this.moment(this.bloqueio.data_de_bloqueio, 'DD/MM/YYYY')))) {
        this.addErrors('data_retorno', 'A data de retorno deve ser igual ou posterior a data de bloqueio');
        valid = false;
      } else if (this.moment(this.dataRetornoFormatted, 'DD/MM/YYYY').isAfter(this.moment(this.dataAtual, 'DD/MM/YYYY'))) {
        this.addErrors('data_retorno', 'A data de retorno deve ser inferior ou igual a data atual');
        valid = false;
      }

      if (this.bloqueio.quilometragem_retorno == null) {
        this.addErrors('quilometragem_retorno', 'A quilometragem de retorno do veículo deve ser informada');
        valid = false;
      } else if (this.bloqueio.quilometragem_retorno <= this.bloqueio.quilometragem_saida) {
        this.addErrors('quilometragem_retorno', 'A quilometragem de retorno não foi informada corretamente');
        valid = false;
      }
      if (valid) {
        this.bloqueio.data_de_bloqueio = this.moment(this.moment(this.bloqueio.data_de_bloqueio, 'DD/MM/YYYY')).format('YYYY-MM-DD');
        this.bloqueio.data_previsao_retorno = this.moment(this.moment(this.bloqueio.data_previsao_retorno, 'DD/MM/YYYY')).format('YYYY-MM-DD');

        Bloqueio.alterar(this.bloqueio).then((result) => {
          this.veiculo.bloqueio_ativo = null;
          this.veiculo.quilometragem_anterior = this.veiculo.quilometragem_atual;
          const km = this.bloqueio.quilometragem_retorno - this.veiculo.quilometragem_atual;
          this.veiculo.quilometragem_retorno = this.veiculo.quilometragem_atual + km;
          Veiculo.alterar(this.veiculo).then(() => {
            this.limpar();
            this.dialogDesbloqueio = false;
            this.bloqueio.motivo = 'Manutenção';

            this.alertS = true;
            this.messageS = 'Desbloqueio realizado com sucesso';


            this.listarBloqueios();
            this.listarVeiculos();
          });
        }).catch((err) => {
          if (err.length > 0) {
            this.isErrors(err);
          }
        });
      }
    },
    salvar() {
      this.$validator.validate().then((valid) => {
        if (this.veiculo.bloqueio_ativo !== 'Manutenção') {
          this.addErrors('quilometragem_atual', 'Quilometragem inferior a quilometragem registrada atualmente');
        }
        if (this.bloqueio.quilometragem_atual > this.veiculo.quilometragem_atual) {
          this.addErrors('veiculo_id', 'Veículo já se encontra bloqueado para manutenção');
        }
        if (valid) {
          Veiculo.show(this.bloqueio.veiculo_id).then((result) => {
            this.veiculo = result.data;
          });

          if (this.id === 0) {
            if (this.bloqueio.motivo === 'Manutenção') {
              this.bloqueio.quilometragem_saida = this.bloqueio.quilometragem_atual;
            } else {
              this.bloqueio.quilometragem_baixa = this.bloqueio.quilometragem_atual;
            }

            Bloqueio.salvar(this.bloqueio).then((result) => {
              this.veiculo.bloqueio_ativo = this.bloqueio.motivo;
              this.veiculo.quilometragem_anterior = this.veiculo.quilometragem_atual;
              const km = this.bloqueio.quilometragem_atual - this.veiculo.quilometragem_atual;
              this.veiculo.quilometragem_atual = this.veiculo.quilometragem_atual + km;
              Veiculo.alterar(this.veiculo).then((resultVeiculo) => {
                this.limpar();
                this.dialogBloqueio = false;
                this.bloqueio.motivo = 'Manutenção';

                this.alertS = true;
                this.messageS = 'Bloqueio realizado com sucesso';

                this.listarBloqueios();
                this.listarVeiculos();
              });
            }).catch((err) => {
              if (err.length > 0) {
                this.isErrors(err);
              }
            });
          }
        }
      });
    },
    detalhes(item) {
      this.bloqueio = item;
      this.id = this.bloqueio.id;
      this.veiculo = this.bloqueio.veiculo;

      this.veiculo.numNome = `${item.veiculo.numero} - ${item.veiculo.modelo}`;

      if (this.bloqueio.motivo === 'Manutenção') {
        this.bloqueio.data_de_bloqueio = this.moment(this.bloqueio.data_de_bloqueio, 'YYYY-MM-DD').format('DD/MM/YYYY');
        this.bloqueio.data_previsao_retorno = this.moment(this.bloqueio.data_previsao_retorno, 'YYYY-MM-DD').format('DD/MM/YYYY');
        this.bloqueio.data_retorno = this.moment(this.bloqueio.data_retorno, 'YYYY-MM-DD').format('DD/MM/YYYY');
      } else {
        this.bloqueio.data_baixa = this.moment(this.bloqueio.data_baixa, 'YYYY-MM-DD').format('DD/MM/YYYY');
      }

      this.dialogDetalhesBloqueio = true;
    },
    desbloqueio(item) {
      this.bloqueio.data_retorno = null;
      this.bloqueio.quilometragem_retorno = null;

      if (!this.bloqueio.veiculo_id) {
        this.bloqueio = item;
        this.id = this.bloqueio.id;

        this.veiculo = this.bloqueio.veiculo;

        this.veiculo.numNome = `${item.veiculo.numero} - ${item.veiculo.modelo}`;
        this.bloqueio.quilometragem_atual = this.bloqueio.quilometragem_saida;

        this.bloqueio.data_de_bloqueio = this.moment(this.moment(this.bloqueio.data_de_bloqueio, 'YYYY-MM-DD')).format('DD/MM/YYYY');
        this.bloqueio.data_previsao_retorno = this.moment(this.bloqueio.data_previsao_retorno, 'YYYY-MM-DD').format('DD/MM/YYYY');
      }
      this.dialogDesbloqueio = true;
    },
    messageDelete(item) {
      this.bloqueio = item;
      this.veiculo = item.veiculo;

      this.dialogConfirmDelete = true;
    },
    deletar() {
      Bloqueio.deletar(this.bloqueio).then((result) => {
        if (result.data) {
          this.veiculo.bloqueio_ativo = null;
          this.veiculo.quilometragem_atual = this.veiculo.quilometragem_anterior;
          this.veiculo.quilometragem_anterior = 0;
          Veiculo.alterar(this.veiculo).then(() => {
            this.limpar();
            this.bloqueio.motivo = 'Manutenção';

            this.alertS = true;
            this.messageS = 'Bloqueio deletado com sucesso';

            this.listarBloqueios();
            this.listarVeiculos();
          });
        } else {
          this.alertE = true;
          this.messageE = 'Não é possivel deletar';
        }
      });
      this.limpar();
    },
    cancelar() {
      this.dialogBloqueio = false;
      this.dialogDesbloqueio = false;
      this.dialogDetalhesBloqueio = false;

      this.listarBloqueios();
      this.listarVeiculos();
    },
  },
};
</script>

