<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-alert
      v-model="alertError"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Localidades
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="50%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Localidades
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="localidade.nome"
                  name="nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                />
              </v-flex>
              <v-flex
                xs12
                md9
              >
                <v-select
                  v-model="localidade.estado"
                  name="estado"
                  label="* Estado"
                  :items="estados"
                  :error-messages="errors.first('estado')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="localidade.distancia"
                  type="number"
                  name="distancia"
                  label="* Distancia"
                  :error-messages="errors.first('distancia')"
                  hint="km"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar esta localidade?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="localidades"
            :search="search"
          >
            <template v-slot:items="props">
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.estado }}
              </td>
              <td class="text-xs-left">
                {{ props.item.distancia }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-localidade />
  </v-container>
</template>

<script>
import Localidade from '../../services/localidades';
import HelpLocalidade from './HelpLocalidade.vue';

export default {
  components: { HelpLocalidade },
  data: () => ({
    alert: false,
    alertError: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    search: '',
    headers: [
      { text: 'Cidade', value: 'nome' },
      { text: 'Estado', value: 'estado' },
      { text: 'Distancia (km)', value: 'distancia' },
    ],
    estados: ['Paraná', 'São Paulo', 'Mato Grosso', 'Mato Grosso do Sul'],
    id: 0,
    localidade: {
      nome: '',
      estado: '',
      distancia: '',
    },
    localidades: [],
  }),
  mounted() {
    this.listarLocalidades();
  },
  methods: {
    listarLocalidades() {
      Localidade.listar().then((result) => {
        this.localidades = result.data;
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.localidade = {};
      this.errors.clear();
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
      if (this.localidade.distancia <= 0) {
        this.errors.add({
          field: 'distancia',
          msg: 'A distância informada está inválida',
        });
      }
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Localidade.salvar(this.localidade).then((result) => {
          this.limpar();
          this.listarLocalidades();

          this.message = 'Localidade adicionada com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      } else {
        Localidade.alterar(this.localidade).then((result) => {
          this.limpar();
          this.listarLocalidades();

          this.message = 'Localidade alterada com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;
      this.localidade = item;
      this.dialog = true;
    },
    messageDelete(item) {
      this.localidade = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      this.alertError = false;

      Localidade.deletar(this.localidade).then((result) => {
        if (result.data) {
          this.alert = true;
          this.message = 'Localidade deletada com sucesso';

          this.listarLocalidades();
        } else {
          this.alertError = true;
          this.message = 'Não é possivel deletar, pois a localidade está vinculada a um ou mais locais de consulta';
        }
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
