<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-alert
      v-model="alertE"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ messageE }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Veículos
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="55%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Veículo
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex
                xs12
                md5
              >
                <v-text-field
                  v-model="veiculo.marca"
                  name="Marca"
                  label="* Marca"
                  :error-messages="errors.first('marca')"
                />
              </v-flex>
              <v-flex
                xs12
                md5
              >
                <v-text-field
                  v-model="veiculo.modelo"
                  name="Modelo"
                  label="* Modelo"
                  :error-messages="errors.first('modelo')"
                />
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="veiculo.quilometragem_atual"
                  name="quilometragem_atual"
                  label="* Quilometragem"
                  :error-messages="errors.first('quilometragem_atual')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="veiculo.numero"
                  type="number"
                  name="Numero"
                  label="* Número"
                  :error-messages="errors.first('numero')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="veiculo.ano"
                  name="Ano"
                  label="* Ano"
                  :mask="mask_ano"
                  :error-messages="errors.first('ano')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="veiculo.placa"
                  name="Placa"
                  label="* Placa"
                  :mask="mask_placa"
                  :error-messages="errors.first('placa')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="veiculo.capacidade"
                  name="Capacidade"
                  label="* Capacidade"
                  :mask="mask_capacidade"
                  :error-messages="errors.first('capacidade')"
                  hint="Não inclui o motorista"
                />
              </v-flex>
              <template v-if="id > 0 && bloqueios.length > 0">
                <v-flex
                  xs12
                  md3
                >
                  <v-text-field
                    :value="veiculo.bloqueio_ativo != null ? 'SIM' : 'NÃO'"
                    name="bloqueado"
                    label="Bloqueado"
                    readonly
                  />
                </v-flex>
                <v-flex
                  xs12
                  md3
                >
                  <v-text-field
                    v-model="veiculo.bloqueio_ativo"
                    name="tipo_bloqueio"
                    label="Tipo Bloqueio"
                  />
                </v-flex>
              </template>
              <v-flex
                v-if="bloqueios.length > 0"
                xs12
              >
                <v-expansion-panel popout>
                  <v-expansion-panel-content>
                    <div slot="header">
                      Bloqueios
                    </div>
                    <v-container>
                      <v-data-table
                        :headers="headers_bloqueios"
                        :items="bloqueios"
                        item-key="id"
                        hide-actions
                      >
                        <template v-slot:items="props">
                          <td class="text-xs-left">
                            {{ moment(props.item.data_de_bloqueio).format('DD/MM/YYYY') }}
                          </td>
                          <td class="text-xs-left">
                            {{ moment(props.item.data_previsao_retorno).format('DD/MM/YYYY') }}
                          </td>
                          <td class="text-xs-left">
                            {{ moment(props.item.data_retorno).format('DD/MM/YYYY') }}
                          </td>
                        </template>
                      </v-data-table>
                    </v-container>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-flex>
              <v-flex xs10>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar este Veículo?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="veiculos"
            :search="search"
          >
            <template v-slot:items="props">
              <td>{{ props.item.numero }}</td>
              <td class="text-xs-left">
                {{ props.item.modelo }}
              </td>
              <td class="text-xs-left">
                {{ props.item.ano }}
              </td>
              <td class="text-xs-left">
                {{ maskPlaca(props.item.placa) }}
              </td>
              <td class="text-xs-left">
                {{ props.item.capacidade }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-veiculo />
  </v-container>
</template>

<script>
import Veiculo from '../../services/veiculos';
import Bloqueio from '../../services/bloqueios';
import HelpVeiculo from './HelpVeiculo.vue';

export default {
  components: {
    HelpVeiculo,
  },
  data: () => ({
    alert: false,
    alertE: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    messageE: '',
    mask_placa: 'AAA-####',
    mask_ano: '####',
    mask_capacidade: '##',
    search: '',
    headers: [
      { text: 'Número', value: 'numero' },
      { text: 'Modelo', value: 'modelo' },
      { text: 'Ano', value: 'ano' },
      { text: 'Placa', value: 'placa' },
      { text: 'Capacidade', value: 'capacidade' },
    ],
    headers_bloqueios: [
      { text: 'Data Saida', value: 'numero' },
      { text: 'Data Retorno', value: 'modelo' },
      { text: 'Tipo', value: 'tipo' },
    ],
    id: 0,
    veiculo: {
      marca: '',
      modelo: '',
      numero: '',
      placa: '',
      capacidade: null,
      ano: null,
      quilometragem_atual: null,
      bloqueio_ativo: null,
    },
    veiculos: [],
    bloqueios: [],
  }),
  mounted() {
    this.listarVeiculos();
  },
  methods: {
    maskPlaca(data) {
      return `${data.substr(0, 3)}-${data.substr(3, 6)}`;
    },
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        this.veiculos = result.data;
      });
    },
    limpar() {
      this.alert = false;
      this.alertE = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.veiculo = {};
      this.errors.clear();
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Veiculo.salvar(this.veiculo).then(() => {
          this.limpar();
          this.listarVeiculos();

          this.message = 'Veículo adicionado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      } else {
        Veiculo.alterar(this.veiculo).then(() => {
          this.limpar();
          this.listarVeiculos();

          this.message = 'Veículo alterado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;
      this.veiculo = item;

      Bloqueio.listarPorVeiculo(this.id).then((result) => {
        if (result.data) {
          this.bloqueios = result.data;
        }
      });

      this.dialog = true;
    },
    messageDelete(item) {
      this.veiculo = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Veiculo.deletar(this.veiculo).then((result) => {
        if (result.data) {
          this.alert = true;
          this.message = 'Veículo deletado com sucesso';

          this.listarVeiculos();
        } else {
          this.alertE = true;
          this.messageE = 'Não é possivel deletar, pois o veículo está vinculado a uma ou mais viagens';
        }
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
