<template>
  <v-container>
    <!-- MESSAGES -->
    <v-alert
      v-model="alertS"
      transition="scale-transition"
      origin="center center"
      class="success"
      type="success"
      dismissible
      dark
    >
      {{ messageS }}
    </v-alert>
    <v-alert
      v-model="alertE"
      transition="scale-transition"
      origin="center center"
      class="error"
      type="error"
      dismissible
      dark
    >
      {{ messageE }}
    </v-alert>
    <v-layout>
      <v-spacer />
      <v-btn
        color="black"
        flat
        outline
        :to="'/viagens/emergencia'"
      >
        <v-icon>report</v-icon>Emergência
      </v-btn>
      <v-btn
        color="black"
        flat
        outline
        :to="'/viagens/fixa'"
      >
        <v-icon>access_time</v-icon>Fixa
      </v-btn>
    </v-layout>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Viagens
      </v-toolbar-title>
      <v-spacer />
    </v-toolbar>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="viagens"
            :search="search"
          >
            <template v-slot:items="props">
              <td>
                <v-icon v-if="props.item.tipo == 'Emergencia'">
                  report
                </v-icon>
                <v-icon v-if="props.item.tipo == 'Fixa'">
                  history
                </v-icon>
              </td>
              <td class="text-xs-left">
                {{ moment(props.item.data).format('DD/MM/YYYY') }}
              </td>
              <td class="text-xs-left">
                {{ props.item.horario }}
              </td>
              <td class="text-xs-left">
                {{ props.item.localidade.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.motorista.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.status }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="detalhes(props.item)"
                >
                  <v-icon small>
                    list_alt
                  </v-icon>
                </v-btn>
                <v-btn
                  v-if="props.item.status == 'Agendada'"
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
                <v-btn
                  v-else
                  small
                  outline
                  icon
                  disabled
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar esta viagem?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="dialogConfirmDelete = false"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <!-- Detalhes da viagem -->
    <template>
      <v-dialog
        v-model="dialog"
        max-width="55%"
        persistent
        transition="dialog-transition"
      >
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Detalhes viagem
          </v-toolbar-title>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <!-- VEICULO -->
            <v-alert
              v-model="alertWV"
              transition="scale-transition"
              origin="center center"
              color="warning"
              type="warning"
              dismissible
              dark
            >
              {{ messageWV }}
            </v-alert>
            <v-alert
              v-model="alertEV"
              transition="scale-transition"
              origin="center center"
              color="error"
              type="error"
              dismissible
              dark
            >
              {{ messageEV }}
            </v-alert>
            <!-- MOTORISTA -->
            <v-alert
              v-model="alertWM"
              transition="scale-transition"
              origin="center center"
              color="warning"
              type="warning"
              dismissible
              dark
            >
              {{ messageWM }}
            </v-alert>
            <v-alert
              v-model="alertEM"
              transition="scale-transition"
              origin="center center"
              color="error"
              type="error"
              dismissible
              dark
            >
              {{ messageEM }}
            </v-alert>
            <v-layout
              row
              wrap
              class="box-form"
            >
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="viagem.data"
                  name="data"
                  label="Data"
                />
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="viagem.horario"
                  name="horario"
                  label="Horário"
                />
              </v-flex>
              <v-flex
                xs12
                md8
              >
                <v-text-field
                  v-model="viagem.localidade.nome"
                  name="localidade"
                  label="Localidade"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  v-model="viagemVeiculo"
                  name="veiculo"
                  label="Veículo"
                />
              </v-flex>
              <v-flex
                xs10
                md9
              >
                <v-text-field
                  v-model="viagem.motorista.nome"
                  name="motorista"
                  label="Motorista"
                />
              </v-flex>
              <v-flex
                xs2
                md3
              >
                <v-text-field
                  v-model="motoristaCelular"
                  name="motoristaCelular"
                  label="Celular"
                />
              </v-flex>
              <template v-if="viagem.tipo =='Emergencia'">
                <v-flex
                  xs10
                  md9
                >
                  <v-text-field
                    v-model="agendamento.paciente.nome"
                    name="paciente"
                    label="Paciente"
                  />
                </v-flex>
                <v-flex
                  xs2
                  md3
                >
                  <v-text-field
                    v-model="pacienteCelular"
                    name="pacienteCelular"
                    label="Celular"
                  />
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="agendamento.local_de_consulta.nome"
                    name="localDeConsulta"
                    label="Local de consulta"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md6
                >
                  <v-text-field
                    v-model="agendamento.especialidade.nome"
                    name="especialidade"
                    label="Especialidade"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md6
                >
                  <v-text-field
                    v-model="agendamento.cid.nome"
                    name="cid"
                    label="Cid 10"
                  />
                </v-flex>
                <v-flex xs12>
                  <label> Procedimentos</label><br>
                  <v-chip
                    v-for="item in agendamento.procedimentos"
                    :key="item.id"
                    outline
                    color="primary"
                  >
                    {{ item.nome }}
                  </v-chip>
                </v-flex>
              </template>
            </v-layout>
            <template v-if="alterar">
              <v-flex xs12>
                <v-autocomplete
                  v-model="viagem.motorista_id"
                  name="motorista_id"
                  label="Motorista"
                  data-vv-name="motorista_id"
                  :error-messages="errors.first('motorista_id')"
                  :items="motoristas"
                  item-text="nome"
                  item-value="id"
                />
              </v-flex>
              <v-flex xs12>
                <v-autocomplete
                  v-model="viagem.veiculo_id"
                  name="veiculo_id"
                  label="Veículo"
                  data-vv-name="veiculo_id"
                  :error-messages="errors.first('veiculo_id')"
                  :items="veiculos"
                  item-text="numModCap"
                  item-value="id"
                  hint="número - modelo - capacidade"
                />
              </v-flex>
            </template>
            <br>
            <v-flex xs12>
              <template v-if="viagem.tipo == 'Fixa' && viagem.status == 'Agendada'">
                <v-btn
                  v-if="!alterar"
                  color="primary"
                  outline
                  flat
                  @click="ativaAlterar"
                >
                  Alterar
                </v-btn>
                <v-btn
                  v-if="alterar"
                  color="primary"
                  outline
                  flat
                  @click="salvarFixa"
                >
                  Salvar
                </v-btn>
              </template>
              <v-btn
                v-if="viagem.tipo == 'Emergencia' && viagem.status == 'Agendada'"
                color="primary"
                outline
                flat
                :to="`/viagens/emergencia/${viagem.id}`"
              >
                Editar
              </v-btn>
              <v-btn
                v-if="viagem.status == 'Agendada'"
                color="black"
                outline
                flat
                @click="messageDelete(viagem)"
              >
                Deletar
              </v-btn>
              <v-btn
                color="red"
                outline
                flat
                @click="cancelar"
              >
                Cancelar
              </v-btn>
            </v-flex>
          </v-container>
        </v-card>
      </v-dialog>
    </template>
  </v-container>
</template>

<script>
import Viagem from '../../services/viagens';
import Motorista from '../../services/motoristas';
import Veiculo from '../../services/veiculos';
import Agendamento from '../../services/agendamentos';

export default {
  data: () => ({
    validE: true,
    validW: true,
    dialog: false,
    dialogConfirmDelete: false,
    alertE: false,
    alertS: false,
    alterar: false,
    alertEV: false,
    alertWV: false,
    alertEM: false,
    alertWM: false,
    messageEV: '',
    messageWV: '',
    messageEM: '',
    messageWM: '',
    messageE: '',
    messageS: '',
    search: '',
    headers: [
      {},
      { text: 'Data', value: 'data' },
      { text: 'Horário', value: 'horario' },
      { text: 'Localidade', value: 'localidade' },
      { text: 'Motorista', value: 'motorista' },
      { text: 'Status', value: 'status' },
    ],
    veiculo: {
      id: null,
      numModCap: null,
    },
    viagem: {
      id: null,
      data: null,
      horario: null,
      localidade: { nome: '' },
      motorista: { nome: '', celular: '' },
      motorista_id: null,
      veiculo: { nome: '', capacidade: null },
      veiculo_id: null,
      tipo: null,
      agendamentos: [],
    },
    viagemVeiculo: '',
    motoristaCelular: null,
    agendamento: {
      id: null,
      paciente: { nome: '' },
      paciente_id: null,
      local_de_consulta: { nome: '' },
      local_de_consulta_id: null,
      especialidade: { nome: '' },
      especialidade_id: null,
      cid: { nome: '' },
      cid_id: null,
      procedimentos: [],
    },
    pacienteCelular: null,
    // Listas
    veiculos: [],
    motoristas: [],
    viagens: [],
  }),
  watch: {
    // eslint-disable-next-line func-names
    'viagem.veiculo_id': function () {
      this.buscarPorVeiculoPeriodo();
    },
    // eslint-disable-next-line func-names
    'viagem.motorista_id': function () {
      this.buscarPorMotoristaPeriodo();
    },
  },
  mounted() {
    this.listarViagens();
  },
  methods: {
    buscarPorVeiculoPeriodo() {
      if (this.viagem.veiculo_id != null) {
        this.alertE = false;
        const data = this.moment(this.viagem.data, 'DD/MM/YYYY').format('YYYY-MM-DD');
        Viagem.buscarPorVeiculoPeriodo(data, data, this.viagem.veiculo_id, null, this.viagem.id)
          .then((result) => {
            if (result.data.length > 0) {
              const itens = result.data.filter(item => !this.moment(this.viagem.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                && !this.moment(this.viagem.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
              if (itens.length > 0) {
                this.listaViagens = [];
                this.alertWV = false;
                this.alertEV = true;
                this.validE = false;
                this.messageEV = 'Existem viagens para o veículo no periodo informado';
              } else {
                this.alertEV = false;
                this.alertWV = true;
                this.validW = false;
                this.messageWV = 'Existem viagens para o veículo no periodo informado, porém o horário informado difere de 6 horas do horário dessas viagens';
              }
            } else {
              this.validE = true;
              this.validW = true;
              this.alertEV = false;
              this.alertWV = false;
            }
          });
      }
    },
    buscarPorMotoristaPeriodo() {
      if (this.viagem.motorista_id != null) {
        this.alertE = false;
        const data = this.moment(this.viagem.data, 'DD/MM/YYYY').format('YYYY-MM-DD');
        Viagem
          .buscarPorMotoristaPeriodo(data, data, this.viagem.motorista_id, null, this.viagem.id)
          .then((result) => {
            if (result.data.length > 0) {
              const itens = result.data.filter(item => !this.moment(this.viagem.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                && !this.moment(this.viagem.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
              if (itens.length > 0) {
                this.listaViagens = [];
                this.alertWM = false;
                this.alertEM = true;
                this.validE = false;
                this.messageEM = 'Existem viagens para o motorista no periodo informado';
              } else {
                this.alertEM = false;
                this.alertWM = true;
                this.validW = false;
                this.messageWM = 'Existem viagens para o motorista no periodo informado, porém o horário informado difere de 6 horas do horário dessas viagens';
              }
            } else {
              this.validE = true;
              this.validW = true;
              this.alertEM = false;
              this.alertWM = false;
            }
          });
      }
    },
    listarViagens() {
      Viagem.listar().then((result) => {
        this.viagens = result.data;
      });
    },
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        result.data.forEach((item) => {
          this.veiculo = {
            id: item.id,
            numModCap: `${item.numero} - ${item.modelo} - ${item.capacidade} `,
          };

          this.veiculos.push(this.veiculo);
        });
      });
    },
    listarMotoristas() {
      Motorista.listar().then((result) => {
        this.motoristas = result.data.filter(item => item.nome !== this.motorista);
      });
    },
    limpar() {
      this.alertS = false;
      this.alertE = false;
      this.dialogConfirmDelete = false;
      this.dialog = false;
      this.alterar = false;

      this.messageS = '';
      this.messageE = '';

      this.viagem = {
        id: null,
        data: null,
        horario: null,
        localidade: { nome: '' },
        motorista: { nome: '', celular: '' },
        motorista_id: null,
        veiculo: { nome: '' },
        veiculo_id: null,
        tipo: null,
      };
      this.agendamento = {
        id: null,
        paciente: { nome: '' },
        paciente_id: null,
        local_de_consulta: { nome: '' },
        local_de_consulta_id: null,
        especialidade: { nome: '' },
        especialidade_id: null,
        cid: { nome: '' },
        cid_id: null,
        procedimentos: [],
      };
      this.listarViagens();
    },
    ativaAlterar() {
      // Agendamento.buscaPorViagem(this.viagem.id).then((resultAgendos) => {
      // if (resultAgendos.data.length == 0) {
      this.alterar = !this.alterar;
      //   }
      // });
    },
    detalhes(item) {
      this.viagem = item;

      if (item.status === 'Andamento' || item.status === 'Realizada' || item.status === 'Não Realizada') { this.alterar = false; }
      if (item.tipo === 'Emergencia') {
        Agendamento.buscaPorViagem(item.id).then((result) => {
          this.agendamento.id = result.data[0].id;
          this.agendamento.paciente = result.data[0].paciente;
          this.pacienteCelular = `(${result.data[0].paciente.celular.substr(0, 2)})${result.data[0].paciente.celular.substr(2, 5)}-${result.data[0].paciente.celular.substr(7, 11)}`;

          this.agendamento.paciente_id = result.data[0].paciente_id;
          this.agendamento.local_de_consulta = result.data[0].localDeConsulta;
          this.agendamento.local_de_consulta_id = result.data[0].local_de_consulta_id;
          this.agendamento.especialidade = result.data[0].especialidade;
          this.agendamento.especialidade_id = result.data[0].especialidade_id;
          this.agendamento.cid = result.data[0].cid;
          this.agendamento.cid_id = result.data[0].cid_id;
          this.agendamento.procedimentos = result.data[0].procedimentos;
        });
      }
      this.viagemVeiculo = `${item.veiculo.numero} - ${item.veiculo.modelo} - ${item.veiculo.capacidade} `;
      this.motoristaCelular = `(${item.motorista.celular.substr(0, 2)})${item.motorista.celular.substr(2, 5)}-${item.motorista.celular.substr(7, 11)}`;
      this.viagem.data = this.moment(this.viagem.data, 'YYYY-MM-DD').format(['DD/MM/YYYY']);
      this.viagem.horario = this.moment(this.viagem.horario, 'HH:mm:ss').format('HH:mm');

      this.listarMotoristas();
      this.listarVeiculos();
      this.dialog = true;
    },
    salvarFixa() {
      if (this.viagem.veiculo_id != null || this.viagem.motorista_id != null) {
        Agendamento.buscaPorViagem(this.viagem.id).then((resultAgendos) => {
          if (this.viagem.veiculo.capacidade < resultAgendos.data.length) {
            this.validE = false;
            this.errors.add({
              field: 'veiculo_id',
              msg: 'Veiculo informado possui capacidade inferior a quantidade de agendamentos',
            });
          } else {
            this.validE = true;
          }
        });
        if (this.validE) {
          this.viagem.data = this.moment(this.viagem.data, 'DD/MM/YYYY').format('YYYY-MM-DD');
          Viagem.alterar(this.viagem).then((result) => {
            this.messageS = 'Viagem alterada com sucesso';
            this.listarViagens();
            this.limpar();
          }).catch((err) => {
            if (err.length > 0) {
              this.errors.add({ field: err.field, msg: err.message });
            }
          });
        }
      } else {
        this.dialog = false;
      }
    },
    messageDelete(item) {
      this.auxViagem = item;

      if (item.tipo == 'Emergencia') { // AQUI
        Agendamento.buscaPorViagem(item.id).then((result) => {
          if (result.data.length > 0) {
            this.agendamento.id = result.data[0].id;
          }
        });
      }
      this.dialogConfirmDelete = true;
    },
    deletar() {
      if (this.auxViagem.status === 'Agendada') {
        if (this.auxViagem.tipo === 'Emergencia') {
          Agendamento.deletar(this.agendamento).then((result) => {
            Viagem.deletar(this.auxViagem).then(() => {
              this.limpar();

              this.alertS = true;
              this.messageS = 'Viagem deletada com sucesso';

              this.listarViagens();
            });
          });
        } else {
          Viagem.deletar(this.auxViagem).then((result) => {
            if (result.data) {
              this.limpar();
              this.alertS = true;
              this.messageS = 'Viagem deletada com sucesso';

              this.listarViagens();
            } else {
              this.dialogConfirmDelete = false;
              this.alertE = true;
              this.messageE = 'Não é possivel deletar, pois existem agendamentos vinculados a está viagem';
            }
          });
        }
      } else {
        this.dialogConfirmDelete = false;
        this.alertE = true;
        this.messageE = 'Não é possivel deletar, pois viagem se encontra em andamento ou já foi realizada';
      }
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>

<style>
.box-form {
  padding: 0 5px 0 5px;
  border: 1px solid #d0d0d0;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
</style>
