import { http } from './config';

export default {
  salvar: localDeConsulta => http.post('locaisDeConsulta', localDeConsulta),
  alterar: localDeConsulta => http.put(`locaisDeConsulta/${localDeConsulta.id}`, localDeConsulta),
  deletar: localDeConsulta => http.delete(`locaisDeConsulta/${localDeConsulta.id}`),
  listar: () => http.get('locaisDeConsulta'),
  listarPorLocalidade: localidade => http.get(`localidades/${localidade}/locais`),
};
