<template app>
  <v-container>
    <!-- VEICULO -->
    <v-alert
      v-model="alertWV"
      transition="scale-transition"
      origin="center center"
      color="warning"
      type="warning"
      dismissible
      dark
    >
      {{ messageWV }}
    </v-alert>
    <v-alert
      v-model="alertEV"
      transition="scale-transition"
      origin="center center"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ messageEV }}
    </v-alert>
    <!-- MOTORISTA -->
    <v-alert
      v-model="alertWM"
      transition="scale-transition"
      origin="center center"
      color="warning"
      type="warning"
      dismissible
      dark
    >
      {{ messageWM }}
    </v-alert>
    <v-alert
      v-model="alertEM"
      transition="scale-transition"
      origin="center center"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ messageEM }}
    </v-alert>
    <!-- SUCCESS -->
    <v-alert
      v-model="alertS"
      transition="scale-transition"
      origin="center center"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ messageS }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Agendamento de Viagens de Emergencia
      </v-toolbar-title>
    </v-toolbar>
    <v-card
      flat
      class="pa-3"
    >
      <v-container grid-list-md>
        <v-layout
          row
          wrap
        >
          <input
            ref="dataAtual"
            v-model="dataAtual"
            type="text"
            hidden
          >
          <v-flex
            xs12
            md3
          >
            <v-menu
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="dataFormated"
                v-validate="'required|after:dataAtual|date_format:DD/MM/YYYY'"
                data-vv-name="data"
                name="data"
                :error-messages="errors.first('data')"
                label="* Data"
                prepend-icon="event"
                readonly
              />
              <v-date-picker
                v-model="viagem.data"
                locale="pt-BR"
                data-vv-name="data"
                @input="menu = false"
              />
            </v-menu>
          </v-flex>
          <v-flex
            xs12
            md2
          >
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="viagem.horario"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="viagem.horario"
                  v-validate="'required'"
                  name="horario"
                  data-vv-name="horario"
                  label="* Horário"
                  prepend-icon="access_time"
                  :error-messages="errors.first('horario')"
                  readonly
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-if="menu2"
                v-model="viagem.horario"
                format="24hr"
                data-vv-name="horario"
                full-width
                @click:minute="$refs.menu.save(viagem.horario)"
              />
            </v-menu>
          </v-flex>
          <v-flex
            xs12
            md7
          >
            <v-autocomplete
              v-model="agendamento.paciente_id"
              v-validate="'required'"
              data-vv-name="paciente_id"
              name="paciente_id"
              label="* Paciente"
              :items="pacientes"
              item-text="nome"
              item-value="id"
              :error-messages="errors.first('paciente_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md6
          >
            <v-autocomplete
              v-model="viagem.veiculo_id"
              v-validate="'required'"
              data-vv-name="veiculo_id"
              name="veiculo_id"
              label="* Veículo"
              :items="veiculos"
              item-text="numModCap"
              item-value="id"
              :error-messages="errors.first('veiculo_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md6
          >
            <v-autocomplete
              v-model="viagem.motorista_id"
              v-validate="'required'"
              data-vv-name="motorista_id"
              name="motorista_id"
              label="* Motorista"
              :items="motoristas"
              item-text="nome"
              item-value="id"
              :error-messages="errors.first('motorista_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md5
          >
            <v-autocomplete
              v-model="viagem.localidade_id"
              v-validate="'required'"
              data-vv-name="localidade_id"
              name="localidade_id"
              label="* Localidade"
              :items="localidades"
              item-text="nome"
              item-value="id"
              :error-messages="errors.first('localidade_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md7
          >
            <v-autocomplete
              v-model="agendamento.local_de_consulta_id"
              v-validate="'required'"
              data-vv-name="local_de_consulta_id"
              name="local_de_consulta_id"
              :items="locais"
              label="* Local de consulta"
              item-text="nome"
              item-value="id"
              :error-messages="errors.first('local_de_consulta_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md6
          >
            <v-autocomplete
              v-model="agendamento.especialidade_id"
              v-validate="'required'"
              data-vv-name="especialidade_id"
              name="especialidade_id"
              :items="especialidades"
              item-text="nome"
              item-value="id"
              label="* Especialidade"
              :error-messages="errors.first('especialidade_id')"
            />
          </v-flex>
          <v-flex
            xs12
            md6
          >
            <v-autocomplete
              v-model="agendamento.cid_id"
              v-validate="'required'"
              data-vv-name="cid_id"
              name="cid_id"
              :items="cids"
              item-text="nome"
              item-value="id"
              label="* Cid10"
              :error-messages="errors.first('cid_id')"
            />
          </v-flex>
          <v-flex xs12>
            <v-autocomplete
              v-model="agendamento.procedimentos"
              v-validate="'required'"
              data-vv-name="procedimentos"
              name="procedimentos"
              :items="listaProcedimentos"
              chips
              color="blue-grey lighten-2"
              label="* Procedimentos"
              item-text="nome"
              item-value="id"
              :error-messages="errors.first('procedimentos')"
              multiple
            >
              <template
                slot="selection"
                slot-scope="data"
              >
                <v-chip
                  :selected="data.selected"
                  close
                  class="chip--select-multi"
                  @input="data.parent.selectItem(data.item)"
                >
                  {{ data.item.nome }}
                </v-chip>
              </template>
              <template
                slot="item"
                slot-scope="data"
              >
                <template>
                  <v-list-tile-content>
                    <v-list-tile-title v-html="data.item.nome" />
                  </v-list-tile-content>
                </template>
              </template>
            </v-autocomplete>
          </v-flex>
          <v-flex xs12>
            <v-btn
              v-if="!altera"
              color="blue darken-1"
              outline
              dark
              @click="salvar"
            >
              Salvar
            </v-btn>
            <v-btn
              v-if="!altera"
              color="red darken-1"
              outline
              dark
              @click="cancelar"
            >
              Cancelar
            </v-btn>
            <v-btn
              color="grey darken-1"
              outline
              dark
              :to="'/viagens'"
            >
              Listar
            </v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
    <help-emergencia />
  </v-container>
</template>

<script>
import { Validator } from 'vee-validate';

import Paciente from '../../services/pacientes';
import Veiculo from '../../services/veiculos';
import Motorista from '../../services/motoristas';
import Localidade from '../../services/localidades';
import LocalDeConsulta from '../../services/locaisDeConsulta';
import Especialidade from '../../services/especialidades';
import Cid from '../../services/cids';
import Procedimento from '../../services/procedimentos';
import Viagem from '../../services/viagens';
import Agendamento from '../../services/agendamentos';
import HelpEmergencia from './HelpEmergencia.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpEmergencia,
  },
  data: () => ({
    altera: false,
    menu: false,
    menu2: false,
    progress: false,
    alertS: false,
    alertEV: false,
    alertWV: false,
    alertEM: false,
    alertWM: false,
    messageS: '',
    messageEV: '',
    messageWV: '',
    messageEM: '',
    messageWM: '',
    dataAtual: null,
    veiculo: {
      id: null,
      numModCap: null,
    },
    viagem_id: 0,
    viagem: {
      data: null,
      horario: null,
      veiculo_id: null,
      localidade_id: null,
      motorista_id: null,
      tipo: 'Emergencia',
      status: 'Agendada', // 'Agendada' mudar para sting no banco
    },
    agendamento_id: 0,
    agendamento: {
      id: 0,
      paciente_id: null,
      local_de_consulta_id: null,
      especialidade_id: null,
      cid_id: null,
      procedimentos: [],
      viagem_id: null,
    },
    // Listas
    pacientes: [],
    veiculos: [],
    motoristas: [],
    localidades: [],
    locais: [],
    especialidades: [],
    cids: [],
    listaProcedimentos: [],
    // Validações
    dictionary: {
      custom: {
        horario: {
          is_time: 'O horário foi informado está inválido',
          required: 'O horário deve ser informado',
          min: 'O horário não foi informado corretamente',
        },
        data: {
          required: 'A data deve ser informada',
          after: 'Data deve ser maior ou igual a data atual',
        },
        paciente_id: {
          required: 'O paciente deve ser informado',
        },
        veiculo_id: {
          required: 'O veículo deve ser informado',
        },
        motorista_id: {
          required: 'O motorista deve ser informado',
        },
        localidade_id: {
          required: 'O horário deve ser informado',
        },
        local_de_consulta_id: {
          required: 'O local de consulta deve ser informado',
        },
        especialidade_id: {
          required: 'O horário deve ser informado',
        },
        cid_id: {
          required: 'O Cid10 deve ser informado',
        },
        procedimentos: {
          required: 'O(s) procedimento(s) devem ser informado(s)',
        },
      },
    },
  }),
  computed: {
    dataFormated() {
      return this.viagem.data != null ? this.moment(this.viagem.data).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'agendamento.especialidade_id': function () {
      if (this.agendamento.especialidade_id != null) {
        this.listarCids();
      }
    },
    // eslint-disable-next-line func-names
    'viagem.localidade_id': function () {
      if (this.viagem.localidade_id) {
        this.listarLocais();
      }
    },
    // eslint-disable-next-line func-names
    'agendamento.paciente_id': function () {
      if (this.agendamento.paciente_id && this.viagem.data != null) {
        this.agendamento.paciente_id = this.agendamento.paciente_id;
        this.agendamento.data = this.viagem.data;
        Agendamento.buscaPorPaciente(this.agendamento.paciente_id, this.agendamento.data, this.agendamento.id).then((result) => {
          if (result.data.length > 0) {
            this.errors.add({ field: 'paciente_id', msg: 'Ja existe agendamento para o paciente na data informada' });
          }
        });
      }
    },
    // eslint-disable-next-line func-names
    'viagem.motorista_id': function () {
      if (this.viagem.motorista_id) {
        this.buscarPorMotoristaPeriodo();
      }
    },
    // eslint-disable-next-line func-names
    'viagem.veiculo_id': function () {
      if (this.viagem.veiculo_id) {
        this.buscarPorVeiculoPeriodo();
      }
    },
    // eslint-disable-next-line func-names
    'viagem.horario': function () {
      if (this.viagem.horario.length > 4) {
        this.buscarPorVeiculoPeriodo();
        this.buscarPorMotoristaPeriodo();
      }
    },
    // eslint-disable-next-line func-names
    'viagem.data': function () {
      if (this.viagem.data) {
        this.buscarPorVeiculoPeriodo();
        this.buscarPorMotoristaPeriodo();
      }
    },
  },
  created() {
    Validator.extend('is_time', {
      getMessage: field => 'The format must be HH:MM',
      validate: value => new Promise((resolve) => {
        const regex = new RegExp('^([0-9]|0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$');

        resolve({
          valid: value && regex.test(value),
        });
      }),
    });
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);
    this.dataAtual = this.moment(this.moment().subtract(1, 'days')).format('DD/MM/YYYY');

    this.listarPacientes();
    this.listarVeiculos();
    this.listarMotoristas();
    this.listarLocalidades();
    this.listarEspecialidades();
    this.listarProcedimentos();

    this.viagem_id = this.$route.params.id;
    if (this.viagem_id) {
      Viagem.show(this.viagem_id).then((result) => {
        if (result.data.status == 'Agendada') {
          const data = result.data.agendamentos[0];
          this.agendamento = data;
          this.viagem = result.data;

          this.agendamento_id = this.agendamento.id;
          this.viagem.horario = this.viagem.horario.substr(0, 5);
          this.localidade_id = this.localidade_id;
          this.agendamento.especialidade_id = this.agendamento.especialidade_id;

          this.agendamento.procedimentos = this.agendamento.procedimentos.map(item => item.id);
        } else {
          this.alertEV = true;
          this.altera = true;
          this.messageEV = `Viagem com status igual a ${result.data.status}, não é possível alterar`;
        }
      }).catch((err) => {
        console.log(err);
      });
    } else {
      this.viagem_id = 0;
    }
  },
  methods: {
    listarPacientes() {
      Paciente.listar().then((result) => {
        this.pacientes = result.data;
      });
    },
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        result.data.forEach((item) => {
          if (item.bloqueio_ativo != 'Sucateado'
            && item.bloqueio_ativo != 'Manutenção'
            && item.bloqueio_ativo != 'Venda') {
            this.veiculo = {
              id: item.id,
              numModCap: `${item.numero} - ${item.modelo} - ${item.capacidade}`,
            };

            this.veiculos.push(this.veiculo);
          }
        });
      });
    },
    listarMotoristas() {
      Motorista.listar().then((result) => {
        this.motoristas = result.data;
      });
    },
    listarLocalidades() {
      Localidade.listar().then((result) => {
        this.localidades = result.data;
      });
    },
    listarLocais() {
      LocalDeConsulta.listarPorLocalidade(this.viagem.localidade_id).then((result) => {
        this.locais = result.data;
      });
    },
    listarEspecialidades() {
      Especialidade.listar().then((result) => {
        this.especialidades = result.data;
      });
    },
    listarCids() {
      Cid.listarPorEspecialidade(this.agendamento.especialidade_id).then((result) => {
        this.cids = result.data;
      });
    },
    listarProcedimentos() {
      Procedimento.listar().then((result) => {
        this.listaProcedimentos = result.data;
      });
    },
    // Validacoes
    validaHorarioViagem(lista) {
      let valid = true;
      let auxHora;
      if (lista.length > 1) {
        for (let i = 0; i < lista.length; i += 1) {
          auxHora = this.moment(lista[i].horario, 'hours').add(6, 'hours');
          for (let j = 1; j < lista.length; j += 1) {
            if (this.moment(this.viagem.horario).isAfter(auxHora)
              && this.moment(this.viagem.horario).isAfter(lista[j].horario)) {
              valid = true;
              auxHora = this.moment(lista[j].horario, 'hours').add(6, 'hours');
            } else { return false; }
          }
        }
      } else {
        return this.moment(this.viagem.horario).isAfter(this.moment(lista[0].horario, 'hours').add(6, 'hours'));
      }
      return valid;
    },
    buscarPorVeiculoPeriodo() {
      if (this.viagem.veiculo_id != null) {
        if (this.viagem.horario != null) {
          if (this.viagem.data != null) {
            Viagem.buscarPorVeiculoPeriodo(
              this.viagem.data,
              this.viagem.data,
              this.viagem.veiculo_id,
              null,
              this.viagem.id,
            )
              .then((result) => {
                if (result.data.length > 0) {
                  const itens = result.data.filter(item => !this.moment(this.viagem.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                    && !this.moment(this.viagem.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
                  if (itens.length > 0) {
                    this.validEM = false; this.alertWV = false; this.alertEV = true;
                    this.messageEV = 'Existe viagem para o veículo na data informada';
                  } else {
                    this.validEM = true;
                    this.alertEV = false;
                    this.alertWV = true;
                    this.messageWV = 'Existem viagem para o veículo na data informada, porém o horário informado difere de 6 horas do horário desta viagem';
                  }
                } else {
                  this.alertEV = false;
                  this.alertWV = false;
                }
              });
          } else { this.errors.add({ field: 'data', msg: 'A data deve ser informada' }); }
        } else { this.errors.add({ field: 'horario', msg: 'O horário deve ser informado' }); }
      }
    },
    buscarPorMotoristaPeriodo() {
      if (this.viagem.motorista_id != null) {
        if (this.viagem.horario != null) {
          if (this.viagem.data != null) {
            this.alertE = false;
            Viagem
              .buscarPorMotoristaPeriodo(
                this.viagem.data,
                this.viagem.data,
                this.viagem.motorista_id,
                null,
                this.viagem.id,
              )
              .then((result) => {
                if (result.data.length > 0) {
                  const itens = result.data.filter(item => !this.moment(this.viagem.horario, 'hours').isAfter(this.moment(item.horario, 'hours').add(6, 'hours'))
                    && !this.moment(this.viagem.horario, 'hours').isBefore(this.moment(item.horario, 'hours').subtract(6, 'hours')));
                  if (itens.length > 0) {
                    this.listaViagens = [];
                    this.validEM = false; this.alertWM = false; this.alertEM = true;
                    this.messageEM = 'Existe viagem para o motorista na data informada';
                  } else {
                    this.validEM = true;
                    this.alertEM = false;
                    this.alertWM = true;
                    this.messageWM = 'Existem viagem para o motorista na data informada, porém o horário informado difere de 6 horas do horário desta viagem';
                  }
                } else {
                  this.alertEM = false;
                  this.alertWM = false;
                }
              });
          } else { this.errors.add({ field: 'data', msg: 'A data  deve ser informada' }); }
        } else { this.errors.add({ field: 'horario', msg: 'O horário deve ser informado' }); }
      }
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    limpar() {
      this.$validator.reset();

      this.progress = false;
      this.alertE = false;
      this.alertS = false;
      this.viagem_id = 0;
      this.viagem = {
        data: null,
        horario: '',
        veiculo_id: null,
        localidade_id: null,
        motorista_id: null,
        tipo: 'Emergencia',
        status: 'Agendada', // 'Agendada' mudar para sting no banco
      };
      this.agendamento_id = 0;
      this.agendamento = {
        paciente_id: null,
        local_de_consulta_id: null,
        especialidade_id: null,
        cid_id: null,
        procedimentos: [],
        viagem_id: 0,
      };
      this.agendamento.procedimentos = [];
      this.agendamento.especialidade_id = null;
      this.localidade_id = null;
    },
    salvar() {
      this.progress = true;
      this.$validator.validate().then((valid) => {
        if (this.viagem.data != null && this.agendamento.paciente_id > 0) {
          this.agendamento.data = this.viagem.data;
          Agendamento.buscaPorPaciente(this.agendamento.paciente_id, this.agendamento.data, this.agendamento.id).then((result) => {
            if (result.data.length > 0) {
              this.errors.add({ field: 'paciente_id', msg: 'Já existe agendamento para o paciente na data informada' });
            }
          });
        }
        if (this.moment(this.viagem.data, 'YYYY-MM-DD').format('YYYY-MM-DD') == this.moment(new Date(), 'YYYY-MM-DD').format('YYYY-MM-DD')) {
          if (this.moment(this.viagem.horario, 'HH:mm').isBefore(this.moment(new Date(), 'HH:mm'))) {
            this.errors.add({
              field: 'horario',
              msg: 'Horário deve ser posterior ao horário atual',
            });
          }
        }
        if (valid) {
          if (this.viagem_id === 0) {
            Viagem.salvar(this.viagem).then((resultViagem) => {
              this.agendamento.viagem_id = resultViagem.data.id;
              this.agendamento.horario = this.viagem.horario;
              Agendamento.salvar(this.agendamento).then((resultAgenda) => {
                this.limpar();

                this.messageS = 'Viagem agendada com sucesso';
                this.alertS = true;
              });
            }).catch((err) => {
              if (err.length > 0) {
                this.isErrors(err);
              }
            });
          } else {
            Viagem.alterar(this.viagem).then((result) => {
              Agendamento.alterar(this.agendamento).then((resultAgenda) => {
                this.limpar();

                this.messageS = 'Viagem e agendamento alterado com sucesso';
                this.alertS = true;
              });
            }).catch((err) => {
              if (err.length > 0) {
                this.isErrors(err);
              }
            });
          }
        } else { this.progress = false; }
      });
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
