<template>
  <v-layout row>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card flat>
        <v-toolbar
          dark
          color="green lighten-4"
          flat
        >
          <v-btn
            dark
            flat
            icon
            outiline
            color="#000"
            @click="close()"
          >
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>{{ title }}</v-toolbar-title>
          <v-spacer />
          <v-toolbar-items>
            <v-btn
              dark
              flat
              icon
              outiline
              color="#000"
              @click="printPdf"
            >
              <v-icon>print</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <div
          id="PrintArea"
          class="padding-20"
        >
          <section class="container basis-numero-container">
            <div class="header">
              <img src="https://iepe.sp.gov.br/Content/img/brasao-iepe.png">
              <span>Relatório das viagens</span>
            </div>
            <hr>
            <div
              v-for="viagem in primeiraPagina"
              :key="viagem.id"
            >
              <div class="container-header">
                <div><strong>Data/Horário: </strong>{{ moment(viagem.data).format('DD/MM/YYYY') }}</div>
                <div><strong>Localidade: </strong>{{ viagem.localidade.nome }} - {{ viagem.localidade.estado }}</div>
              </div>
              <div class="container-header">
                <div><strong>Motorista: </strong>{{ viagem.motorista.nome }}</div>
                <div><strong>Celular: </strong>{{ celularFormatted(viagem.motorista.celular) }}</div>
              </div>
              <div class="container-header">
                <div><strong>Veiculo/Número: </strong>{{ viagem.veiculo.modelo }} - {{ viagem.veiculo.numero }}</div>
                <div><strong>Status: </strong>{{ viagem.status }}</div>
              </div>
              <hr>
            </div>
            <div class="pageNumber">
              <span>DATA: {{ moment(new Date()).format('DD/MM/YYYY') }} - PÁGINA: 1/{{ totalPages }}</span>
            </div>
          </section>

          <section
            v-for="page in pages"
            :key="page.number"
            class="container"
          >
            <div
              v-for="viagem in page.viagens"
              :key="viagem.id"
            >
              <div class="container-header">
                <div><strong>Data/Horário: </strong>{{ moment(viagem.data).format('DD/MM/YYYY') }}</div>
                <div><strong>Localidade: </strong>{{ viagem.localidade.nome }} - {{ viagem.localidade.estado }}</div>
              </div>
              <div class="container-header">
                <div><strong>Motorista: </strong>{{ viagem.motorista.nome }}</div>
                <div><strong>Celular: </strong>{{ celularFormatted(viagem.motorista.celular) }}</div>
              </div>
              <div class="container-header">
                <div><strong>Veiculo/Número: </strong>{{ viagem.veiculo.modelo }} - {{ viagem.veiculo.numero }}</div>
                <div><strong>Status: </strong>{{ viagem.status }}</div>
              </div>
              <hr>
            </div>
            <div class="pageNumber">
              <span>DATA: {{ moment(new Date()).format('DD/MM/YYYY') }} - PÁGINA: {{ page.number }}/{{ totalPages }}</span>
            </div>
          </section>
        </div>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { Printd } from 'printd';

export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    viagens: Array,
    dialog: Boolean,
    // eslint-disable-next-line vue/require-default-prop
    title: String,
  },
  data: () => ({
    totalPages: 1,
    primeiraPagina: [],
    page: { number: '', viagens: [] },
    pages: [],
    cssText: `
    .basis-numero-container {
      flex-wrap: wrap;
    }
    .container {
      width: 21.5cm;
      height: 31cm;
      margin: 0 auto;
    }
    .container-header {
      display: flex;
      width: 100%;
      justify-content: space-between;
    }
    .header {
      display: flex;
      width: 100%;
      height: 80px;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
    }
    .header span {
      font-size: 30px;
      font-weight: bold;
    }
    .header img {
      width: 70px;
      height: 75px;
    }
    .pageNumber {
      font-size: 12px;
      display: flex;
      justify-content: flex-end;
    }`,
  }),
  mounted() {
    this.d = new Printd();
    const { contentWindow } = this.d.getIFrame();

    this.mountPages();
  },
  methods: {
    mountPages() {
      this.primeiraPagina = this.viagens.slice(0, 14);
      if (this.viagens.length > 14) {
        this.totalPages = Math.round(this.viagens.length / 14);
        this.viagens = this.viagens.slice(14, this.viagens.length);

        let pagec = 2;
        for (let index = 0; index < this.viagens.length; index += 14) {
          this.page.number = pagec;
          this.page.viagens = this.viagens.slice(index, index += 14);
          this.pages.push(this.page);
          pagec += 1;
        }
      }
    },
    printPdf() {
      this.d.print(document.getElementById('PrintArea'), [this.cssText]);
    },
    close() {
      this.dialog = false;
      this.$emit('changeDialog', false);
    },
    celularFormatted(celular) {
      const novoCelular = `(${celular.substr(0, 2)}) ${celular.substr(2, 5)} - ${celular.substr(7, 8)}`;
      return novoCelular;
    },
  },
};
</script>


