                  label="* CNH"
                  :mask="mask_cnh"
                  :error-messages="errors.first('cnh')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-menu
                  v-model="menuDate"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
