                >
                  <v-text-field
                    slot="activator"
                    v-model="motorista.validade_cnh"
                    label="* Validade CNH"
                    prepend-icon="event"
                    :error-messages="errors.first('validade_cnh')"
                    readonly
                  />
                  <v-date-picker
                    v-model="motorista.validade_cnh"
                    locale="pt-BR"
                    @input="menuDate = false"
                  />
                </v-menu>
              </v-flex>
              <v-flex
                xs12
                md10
              >
                <v-text-field
                  v-model="motorista.endereco"
                  name="endereco"
                  label="* Endereço"
                  :error-messages="errors.first('endereco')"
                />
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="motorista.numero"
                  name="numero"
                  label="* Número"
                  :error-messages="errors.first('numero')"
                />
              </v-flex>
              <v-flex
                xs12
                md9
              >
                <v-text-field
                  v-model="motorista.bairro"
                  name="bairro"
                  label="* Bairro"
                  :error-messages="errors.first('bairro')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="motorista.cep"
                  name="cep"
                  label="* Cep"
                  :mask="mask_cep"
                  :error-messages="errors.first('cep')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="motorista.celular"
                  name="celular"
                  label="* Celular"
                  :mask="mask_celular"
                  :error-messages="errors.first('celular')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="motorista.celular_coorporativo"
                  name="celular_coorporativo"
                  label="Celular Corporativo"
                  :mask="mask_celular"
                  :error-messages="errors.first('celular_coorporativo')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="motorista.telefone"
                  name="telefone"
                  label="Telefone"
                  :mask="mask_telefone"
                  :error-messages="errors.first('telefone')"
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-menu
                  v-model="menuDate2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="motorista.dt_admissao"
                    label="* Admissão"
                    prepend-icon="event"
                    :error-messages="errors.first('dt_admissao')"
                    readonly
                  />
                  <v-date-picker
                    v-model="motorista.dt_admissao"
                    locale="pt-BR"
                    :error-messages="errors.first('dt_admissao')"
                    @input="menuDate2 = false"
                  />
                </v-menu>
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-menu
                  v-model="menuDate3"
                  :close-on-content-click="false"
                  :nudge-right="40"
