<script>
import Motorista from '../../services/motoristas';
import HelpMotorista from './HelpMotorista.vue';

export default {
  components: {
    HelpMotorista,
  },
  data: () => ({
    menuDate: false,
    menuDate2: false,
    menuDate3: false,
    alert: false,
    alertE: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    messageE: '',
    search: '',
    mask_cpf: '###.###.###-##',
    mask_rg: '########N',
    mask_telefone: '(##)####-####',
    mask_celular: '(##)#####-####',
    mask_cep: '#####-###',
    mask_data: '##/##/####',
    mask_cnh: '###########',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'CPF', value: 'cpf' },
      { text: 'Celular', value: 'celular' },
    ],
    id: 0,
    motorista: {
      nome: '',
      cpf: '',
      rg: '',
      cnh: '',
      validade_cnh: null,
      bairro: '',
      endereco: '',
      numero: '',
      cep: '19640000',
      celular: '',
      celular_coorporativo: '',
      telefone: '',
      dt_admissao: null,
      dt_demissao: null,
    },
    motoristas: [],
  }),
  mounted() {
    this.listarMotoristas();
  },
  methods: {
    listarMotoristas() {
      Motorista.listar().then((result) => {
        this.motoristas = result.data;
      });
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;

      this.motorista = {
        validade_cnh: null,
        cep: '19640000',
        dt_admissao: null,
        dt_demissao: null,
      };

      this.errors.clear();
    },
    salvar() {
      this.progress = true;

      console.log(this.motorista);
      if (this.id === 0) {
        Motorista.salvar(this.motorista).then((result) => {
          this.limpar();
          this.listarMotoristas();

          this.message = 'Motorista adicionado com sucesso';
          this.alert = true;
        }).catch((err) => {
          if (err.response.data.length > 0) {
            this.isErrors(err.response.data);
          }
        });
      } else {
        Motorista.alterar(this.motorista).then((result) => {
          this.limpar();
          this.listarMotoristas();

          this.message = 'Motorista alterado com sucesso';
          this.alert = true;
        }).catch((err) => {
          if (err.response.data.length > 0) {
            this.isErrors(err.response.data);
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;
      this.motorista = item;

      this.motorista.validade_cnh = this.moment(item.validade_cnh).format('YYYY-MM-DD');
      this.motorista.dt_admissao = this.moment(item.dt_admissao).format('YYYY-MM-DD');

      this.dialog = true;
    },
    messageDelete(item) {
      this.motorista = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Motorista.deletar(this.motorista).then((result) => {
        if (result.data) {
          this.listarMotoristas();

          this.message = 'Motorista deletado com sucesso';
          this.alert = true;
        } else {
          this.alertE = true;
          this.messageE = 'Não é possivel deletar, pois o motorista está vinculado a uma ou mais viagens';
        }
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
