<template>
  <v-container>
    <v-alert
      v-model="alertS"
      transition="scale-transition"
      origin="center center"
      dismissible
      dark
      class="success"
      type="success"
    >
      {{ messageS }}
    </v-alert>
    <template>
      <v-toolbar
        flat
        color="green lighten-4"
      >
        <v-toolbar-title class="font-weight-light title text-uppercase">
          Retorno de Veículos
        </v-toolbar-title>
        <v-spacer />
        <v-dialog
          v-model="dialog"
          max-width="60%"
          persistent
          transition="dialog-transition"
        >
          <v-btn
            slot="activator"
            flat
            dark
            icon
            outline
            color="primary"
            class="mb-2"
          >
            <v-icon>add</v-icon>
          </v-btn>
          <v-toolbar
            flat
            color="green lighten-4"
          >
            <v-toolbar-title class="font-weight-light title text-uppercase">
              Retorno
            </v-toolbar-title>
          </v-toolbar>
          <v-card flat>
            <v-container grid-list-md>
              <v-alert
                v-model="alertE"
                transition="scale-transition"
                origin="center center"
                dismissible
                dark
                class="error"
                type="error"
              >
                {{ message }}
              </v-alert>
              <v-layout wrap>
                <v-flex
                  xs12
                  md8
                >
                  <v-autocomplete
                    v-model="veiculo_id"
                    v-validate="'required'"
                    :items="listaVeiculos"
                    name="veiculo_id"
                    data-vv-name="veiculo_id"
                    label="* Veiculo"
                    item-text="numMod"
                    item-value="id"
                    :error-messages="errors.first('veiculo_id')"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-menu
                    v-model="menuData"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="dateRetornoFormatted"
                      v-validate="`required|date_format:DD/MM/YYYY|date_between:${dataSaida},${dataAtual}`"
                      data-vv-name="data_retorno"
                      name="data_retorno"
                      :error-messages="errors.first('data_retorno')"
                      label="* Data"
                      prepend-icon="event"
                      readonly
                    />
                    <v-date-picker
                      v-model="movimentacao.data_retorno"
                      data-vv-name="data_retorno"
                      name="data_retorno"
                      locale="pt-BR"
                      @input="menuData = false"
                    />
                  </v-menu>
                </v-flex>
                <v-flex xs12>
                  <v-expansion-panel
                    v-model="panel"
                    :disabled="disabled"
                    readonly
                    expand
                  >
                    <v-expansion-panel-content disabled>
                      <div slot="header">
                        Dados da saida
                      </div>
                      <v-container>
                        <v-layout
                          row
                          wrap
                        >
                          <v-flex
                            xs12
                            md3
                          >
                            <v-text-field
                              v-model="movimentacao.data_saida"
                              name="data_saida"
                              label="Data da saida"
                              readonly
                            />
                          </v-flex>
                          <v-flex
                            xs12
                            md2
                          >
                            <v-text-field
                              v-model="movimentacao.horario_saida"
                              name="horario_saida"
                              label="Horário da saida"
                              readonly
                            />
                          </v-flex>
                          <v-flex
                            xs12
                            md4
                          >
                            <v-text-field
                              ref="kmSaida"
                              v-model="movimentacao.quilometragem_saida"
                              name="quilometragem_saida"
                              label="Quilometragem na saida"
                              readonly
                            />
                          </v-flex>
                          <v-flex
                            xs12
                            md3
                          >
                            <v-text-field
                              v-model="viagem.horario"
                              name="horario_viagem"
                              label="Horário da viagem"
                              readonly
                            />
                          </v-flex>
                          <v-flex
                            xs12
                            md6
                          >
                            <v-text-field
                              v-model="viagem.localidade.nome"
                              name="localidade"
                              label="Localidade"
                              readonly
                            />
                          </v-flex>
                          <v-flex
                            xs12
                            md6
                          >
                            <v-text-field
                              v-model="viagem.motorista.nome"
                              name="motorista"
                              label="Motorista"
                              readonly
                            />
                          </v-flex>
                        </v-layout>
                      </v-container>
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-flex>
                <v-flex
                  xs12
                  md3
                >
                  <v-menu
                    ref="menu"
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="movimentacao.horario_retorno"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="movimentacao.horario_retorno"
                        v-validate="'required'"
                        name="horario_retorno"
                        data-vv-name="horario_retorno"
                        label="* Horário de retorno"
                        prepend-icon="access_time"
                        :error-messages="errors.first('horario_retorno')"
                        readonly
                        v-on="on"
                      />
                    </template>
                    <v-time-picker
                      v-if="menu2"
                      v-model="movimentacao.horario_retorno"
                      format="24hr"
                      data-vv-name="horario_retorno"
                      full-width
                      @click:minute="$refs.menu.save(movimentacao.horario_retorno)"
                    />
                  </v-menu>
                </v-flex>
                <v-flex
                  xs12
                  md3
                >
                  <v-text-field
                    v-model="movimentacao.quilometragem_retorno"
                    v-validate="'required|kmBefore:kmSaida'"
                    name="quilometragem_retorno"
                    data-vv-name="quilometragem_retorno"
                    label="* Quilometragem retorno"
                    :error-messages="errors.first('quilometragem_retorno')"
                  />
                </v-flex>
                <v-flex xs12>
                  <v-btn
                    v-if="retorno"
                    color="blue darken-1"
                    flat
                    outline
                    @click="salvar"
                  >
                    Salvar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    flat
                    outline
                    @click="cancelar"
                  >
                    Cancelar
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="retornos"
            :search="search"
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td class="text-xs-left">
                {{ props.item.viagem.veiculo.numero }} - {{ props.item.viagem.veiculo.modelo }}
              </td>
              <td class="text-xs-left">
                {{ moment(props.item.data_saida).format('DD/MM/YYYY') }} -
                {{ moment(props.item.horario_saida, 'HH:mm').format('HH:mm') }}
              </td>
              <td class="text-xs-left">
                {{ moment(props.item.data_retorno).format('DD/MM/YYYY') }} -
                {{ moment(props.item.horario_retorno, 'HH:mm').format('HH:mm') }}
              </td>
              <td class="text-xs-left">
                {{ props.item.viagem.localidade.nome }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="detalhes(props.item.id)"
                >
                  <v-icon small>
                    list_alt
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <!-- Detalhes do Retorno -->
    <template>
      <v-dialog
        v-model="dialogDetalhes"
        max-width="60%"
        persistent
        transition="dialog-transition"
      >
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Detalhes retorno
          </v-toolbar-title>
        </v-toolbar>
        <v-card flat>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex
                xs12
                md8
              >
                <v-text-field
                  v-model="veiculo.numMod"
                  label="Veiculo"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md4
              >
                <v-text-field
                  v-model="auxMovimentacao.data_retorno"
                  label="Data do retorno"
                  prepend-icon="event"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="auxMovimentacao.data_saida"
                  label="Data da saida"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md2
              >
                <v-text-field
                  v-model="auxMovimentacao.horario_saida"
                  label="Horário da saida"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md4
              >
                <v-text-field
                  v-model="auxMovimentacao.quilometragem_saida"
                  label="Quilometragem na saida"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="viagem.horario"
                  label="Horário da viagem"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md6
              >
                <v-text-field
                  v-model="viagem.localidade.nome"
                  label="Localidade"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md6
              >
                <v-text-field
                  v-model="viagem.motorista.nome"
                  label="Motorista"
                  readonly
                />
              </v-flex>

              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="auxMovimentacao.horario_retorno"
                  label="Horário de retorno"
                  prepend-icon="access_time"
                  readonly
                />
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="auxMovimentacao.quilometragem_retorno"
                  label="Quilometragem retorno"
                  readonly
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </template>
    <help-retorno />
  </v-container>
</template>

<script>
import { Validator } from 'vee-validate';
import Veiculo from '../../services/veiculos';
import Viagem from '../../services/viagens';
import Retorno from '../../services/retornos';
import Saida from '../../services/saidas';
import HelpRetorno from './HelpRetorno.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpRetorno,
  },
  data: () => ({
    retorno: true,
    search: '',
    alertE: false,
    message: '',
    alertS: false,
    messageS: '',
    time: null,
    menu2: false,
    menuData: false,
    panel: [true, true, false],
    dialog: false,
    dialogDetalhes: false,
    disabled: false,
    headers: [
      { text: 'Veículo', value: 'modelo' },
      { text: 'Saida', value: 'data_saida' },
      { text: 'Retorno', value: 'data_retorno' },
      { text: 'Localidade', value: 'localidade.nome' },
    ],
    dataAtual: null,
    dataSaida: null,
    auxMovimentacao: {
      id: null,
      data_retorno: null,
      horario_retorno: null,
      quilometragem_retorno: null,
      data_saida: null,
      horario_saida: null,
      quilometragem_saida: null,
      viagem: {
        horario: null,
      },
    },
    movimentacao: {
      id: null,
      data_retorno: null,
      horario_retorno: null,
      quilometragem_retorno: null,
      data_saida: null,
      horario_saida: null,
      quilometragem_saida: null,
    },
    veiculo_id: null,
    veiculo: {
      id: null,
      numMod: null,
      quilometragem_atual: null,
      quilometragem_anterior: null,
    },
    viagem: {
      horario: null,
      localidade: { nome: null },
      motorista: { nome: null },
      status: '',
    },
    retornos: [],
    listaVeiculos: [],
    dictionary: {
      custom: {
        veiculo_id: { required: 'O veículo deve ser infromado' },
        data_retorno: {
          required: 'A data de retorno deve ser informada',
          date_between: 'A data de retorno deve estar entre a data de saída e a data atual',
        },
        horario_retorno: {
          required: 'O horário de retorno deve ser informado',
        },
        quilometragem_retorno: {
          required: 'A quilometragem de retorno deve ser informada',
          kmBefore: 'A quilometragem atual é inferior a quilometragem informada na saida',
        },
      },
    },
  }),
  computed: {
    dateRetornoFormatted() {
      return this.movimentacao.data_retorno != null ? this.moment(this.movimentacao.data_retorno).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    veiculo_id() {
      if (this.veiculo_id && this.retorno) {
        this.limparUm();
        Retorno.listarPorVeiculo(this.veiculo_id).then((result) => {
          if (result.data.length > 0) {
            // eslint-disable-next-line prefer-destructuring
            this.movimentacao = result.data[0];

            this.movimentacao.data_saida = this.moment(result.data[0].data_saida).format('DD/MM/YYYY');
            this.dataSaida = this.moment(this.moment(result.data[0].data_saida, 'DD/MM/YYYY').subtract(1, 'day')).format('DD/MM/YYYY');
            this.movimentacao.horario_saida = this.moment(result.data[0].horario_saida, 'HH:mm').format('HH:mm');
            Viagem.show(this.movimentacao.viagem_id).then((viagemResult) => {
              this.viagem = viagemResult.data;
              this.viagem.horario = this.moment(this.viagem.horario, 'HH:mm').format('HH:mm');
            });
          } else {
            this.alertE = true;
            this.message = 'Veículo não possui saída registrada';
          }
        });
      }
    },
    // eslint-disable-next-line func-names
    'movimentacao.data_retorno': function () {
      this.movimentacao.horario_retorno = null;
      this.movimentacao.quilometragem_retorno = null;
    },
  },
  created() {
    Validator.extend('kmBefore', (value, [otherValue]) => value > otherValue, {
      hasTarget: true,
    });
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);

    this.listarSaidas();
    this.listarVeiculos();
    this.dataAtual = this.moment(this.moment().add(1, 'days')).format('DD/MM/YYYY');
  },
  methods: {
    listarVeiculos() {
      Saida.listar('saida').then((result) => {
        result.data.forEach((item) => {
          this.veiculo = {
            id: item.viagem.veiculo.id,
            numMod: `${item.viagem.veiculo.numero} - ${item.viagem.veiculo.modelo}`,
          };

          this.listaVeiculos.push(this.veiculo);
        });
      });
    },
    listarSaidas() {
      const movimento = 'retorno';
      Retorno.listar(movimento).then((result) => {
        this.retornos = result.data.filter(retorno => retorno.data_retorno != null);
      });
    },
    limpar() {
      this.$validator.reset();
      this.dialog = false;
      this.dialogDetalhes = false;

      this.veiculo_id = null;
      this.limparUm();
    },
    limparUm() {
      this.movimentacao.data_retorno = null;
      this.movimentacao.horario_retorno = null;
      this.movimentacao.data_saida = null;
      this.movimentacao.horario_saida = null;
      this.movimentacao.quilometragem_saida = null;
      this.viagem = {
        horario: null,
        localidade: { nome: null },
        motorista: { nome: null },
      };
    },
    salvar() {
      this.$validator.validate().then((valid) => {
        if (this.moment(this.movimentacao.data_retorno).format('DD/MM/YYYY') == this.movimentacao.data_saida) {
          if (this.moment(this.movimentacao.horario_retorno, 'HH:mm').isBefore(this.moment(this.movimentacao.horario_saida, 'HH:mm'))) {
            this.errors.add({
              field: 'horario_retorno',
              msg: 'O horário de retorno deve ser posterior ao horário de saída',
            });
          }
        }
        if (this.veiculo.id != null) {
          Veiculo.show(this.veiculo.id).then((result) => {
            this.veiculo = result.data;
            if (this.movimentacao.quilometragem_retorno < this.veiculo.quilometragem_atual) {
              this.errors.add({
                field: 'quilometragem_retorno',
                msg: 'Quilometragem inferior a quilometragem registrada atualmente',
              });
              valid(false);
            }
          });
        }
        if (valid) {
          this.movimentacao.data_saida = this.moment(this.movimentacao.data_saida, 'DD/MM/YYYY').format('YYYY-MM-DD');

          Retorno.alterar(this.movimentacao).then((result) => {
            this.viagem.status = 'Realizada';
            Viagem.alterar(this.viagem).then((rsViagem) => {
              this.veiculo.quilometragem_anterior = this.veiculo.quilometragem_atual;
              const km = this.movimentacao.quilometragem_retorno - this.veiculo.quilometragem_atual;
              this.veiculo.quilometragem_atual = this.veiculo.quilometragem_atual + km;
              Veiculo.alterar(this.veiculo).then((resultVeiculo) => {
                this.messageS = 'Registro de retorno do veículo realizado com sucesso';
                this.alertS = true;

                this.listarSaidas();
                this.limpar();
              });
            });
          });
        }
      });
    },
    detalhes(id) {
      this.retorno = false;
      Retorno.show(id).then((movimentacao) => {
        this.auxMovimentacao = movimentacao.data;

        this.veiculo.numMod = `${movimentacao.data.viagem.veiculo.numero} - ${movimentacao.data.viagem.veiculo.modelo}`;

        this.auxMovimentacao.data_saida = this.moment(this.auxMovimentacao.data_saida).format('DD/MM/YYYY');
        this.auxMovimentacao.data_retorno = this.moment(this.auxMovimentacao.data_retorno).format('DD/MM/YYYY');

        this.viagem = movimentacao.data.viagem;
        this.viagem.horario = this.moment(movimentacao.data.viagem.horario, 'HH:mm').format('HH:mm');

        this.dialogDetalhes = true;
      });
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
