<template>
  <v-layout row>
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card flat>
        <v-toolbar
          dark
          color="green lighten-4"
          flat
        >
          <v-btn
            dark
            flat
            icon
            outiline
            color="#000"
            @click="close()"
          >
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>{{ title }}</v-toolbar-title>
          <v-spacer />
          <v-toolbar-items>
            <v-btn
              dark
              flat
              icon
              outiline
              color="#000"
              @click="printPdf"
            >
              <v-icon>print</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <div
          id="PrintArea"
          class="padding-20"
        >
          <section class="container basis-numero-container">
            <div class="header">
              <img src="https://iepe.sp.gov.br/Content/img/brasao-iepe.png">
              <span>Relatório da viagem</span>
            </div>
            <hr>
            <div class="container-header">
              <div>
                <div> <strong>Motorista: </strong>{{ viagem.motorista.nome }} - <strong>Telefone: </strong>{{ celularFormatted(viagem.motorista.celular) }}</div>
                <div> <strong>Veiculo: </strong>{{ viagem.veiculo.modelo }} - {{ viagem.veiculo.numero }}</div>
              </div>
              <div>
                <div>
                  <strong>Localidade: </strong><span>{{ viagem.localidade.nome }}</span>
                </div>
                <div>
                  <strong>Data/Horário: </strong><span>{{ moment(viagem.data).format('DD/MM/YYYY') }} - {{ moment(viagem.horario, 'hh:mm:ss').format('hh:mm') }}</span>
                </div>
              </div>
            </div>
            <hr>
            <div
              v-for="agendamento in primeiraPagina"
              :key="agendamento.id"
            >
              <div class="container-header">
                <div><strong>Nome: </strong>{{ agendamento.paciente.nome }}</div>
                <div><strong>Celular: </strong>{{ celularFormatted(agendamento.paciente.celular) }}</div>
              </div>
              <div class="container-header">
                <div>
                  <strong>Local de consulta: </strong> {{ agendamento.localDeConsulta.nome }}
                </div>
                <div><strong>Especialidade: </strong>{{ agendamento.especialidade.nome }}</div>
              </div>
              <div>
                <div> <strong>Procedimentos: </strong></div>
                <template v-for="procedimento in agendamento.procedimentos">
                  <span :key="procedimento.id">
                    {{ procedimento.nome }}
                  </span>
                </template>
              </div>
              <div class="container-header">
                <div><strong>Acompanhante: </strong>{{ agendamento.acompanhante }}</div>
                <div><strong>Presença: </strong> [ ] Sim - [ ] Não</div>
              </div>
              <hr>
            </div>
            <div class="pageNumber">
              <span>DATA: {{ moment(new Date()).format('DD/MM/YYYY') }} - PÁGINA: 1/{{ totalPages }}</span>
            </div>
          </section>

          <section
            v-for="page in pages"
            :key="page.number"
            class="container"
          >
            <div
              v-for="agendamento in page.agendamentos"
              :key="agendamento.id"
            >
              <div class="container-header">
                <div><strong>Nome: </strong>{{ agendamento.paciente.nome }}</div>
                <div><strong>Celular: </strong>{{ celularFormatted(agendamento.paciente.celular) }}</div>
              </div>
              <div class="container-header">
                <div>
                  <strong>Local de consulta: </strong> {{ agendamento.localDeConsulta.nome }}
                </div>
                <div><strong>Especialidade: </strong>{{ agendamento.especialidade.nome }}</div>
              </div>
              <div>
                <div> <strong>Procedimentos: </strong></div>
                <template v-for="procedimento in agendamento.procedimentos">
                  <span :key="procedimento.id">
                    {{ procedimento.nome }}
                  </span>
                </template>
              </div>
              <div class="container-header">
                <div><strong>Acompanhante: </strong>{{ agendamento.acompanhante }}</div>
                <div><strong>Presença: </strong> [ ] Sim - [ ] Não</div>
              </div>
              <hr>
            </div>
            <div class="pageNumber">
              <span>DATA: {{ moment(new Date()).format('DD/MM/YYYY') }} - PÁGINA: {{ page.number }}/{{ totalPages }}</span>
            </div>
          </section>
        </div>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import { Printd } from 'printd';

export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    viagem: Object,
    dialog: Boolean,
    // eslint-disable-next-line vue/require-default-prop
    title: String,
  },
  data: () => ({
    totalPages: 1,
    cssText: `
      .container {
        width: 21cm;
        height: 31cm;
        margin: 0 auto;
        padding: 0;
      }
      .container-header {
        display: flex;
        width: 100%;
        justify-content: space-between;
      }
      .header {
        display: flex;
        height: 80px;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
      }

      .header span {
        font-size: 30px;
        font-weight: bold;
      }

      .header img {
        width: 70px;
        height: 75px;
      }

      .pageNumber {
        font-size: 12px;
        display: flex;
        justify-content: flex-end;
      }
    `,
    primeiraPagina: [],
    page: { number: '', agendamentos: [] },
    pages: [],
  }),
  mounted() {
    this.d = new Printd();
    const { contentWindow } = this.d.getIFrame();

    this.mountPages();
  },
  methods: {
    mountPages() {
      this.primeiraPagina = this.viagem.agendamentos.slice(0, 2);
      if (this.viagem.agendamentos.length > 2) {
        this.totalPages = this.viagem.agendamentos.length / 2;
        this.viagem.agendamentos = this.viagem.agendamentos.slice(2, this.viagem.agendamentos.length);

        let pagec = 2;
        for (let index = 0; index < this.viagem.agendamentos.length; index += 2) {
          this.page.number = pagec;
          this.page.agendamentos = this.viagem.agendamentos.slice(index, index += 2);
          this.pages.push(this.page);
          pagec += 1;
        }
      }
    },
    printPdf() {
      this.d.print(document.getElementById('PrintArea'), [this.cssText]);
    },
    close() {
      this.dialog = false;
      this.$emit('changeDialog', false);
    },
    celularFormatted(celular) {
      const novoCelular = `(${celular.substr(0, 2)}) ${celular.substr(2, 5)} - ${celular.substr(7, 8)}`;
      return novoCelular;
    },
  },
};
</script>

