<template>
  <v-container>
    <v-alert
      v-model="alert"
      transition="scale-transition"
      origin="center center"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Parametrização
      </v-toolbar-title>
    </v-toolbar>
    <v-card
      flat
      class="pa-3"
    >
      <v-container grid-list-md>
        <v-layout
          row
          wrap
        >
          <v-flex
            xs12
            md6
          >
            <v-text-field
              v-model="parametrizacao.nome_empresa"
              v-validate="'required'"
              name="nome_empresa"
              label="* Nome da empresa"
              :error-messages="errors.first('nome_empresa')"
            />
          </v-flex>
          <v-flex
            xs12
            md3
          >
            <v-menu
              ref="menuInicio"
              v-model="menuInicio"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="parametrizacao.expediente_inicio"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  ref="expIni"
                  v-model="parametrizacao.expediente_inicio"
                  v-validate="'required'"
                  name="expediente_inicio"
                  data-vv-name="expediente_inicio"
                  label="* Inicio de expediente"
                  prepend-icon="access_time"
                  :error-messages="errors.first('expediente_inicio')"
                  readonly
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-if="menuInicio"
                v-model="parametrizacao.expediente_inicio"
                format="24hr"
                data-vv-name="expediente_inicio"
                full-width
                @click:minute="$refs.menuInicio.save(parametrizacao.expediente_inicio)"
              />
            </v-menu>
          </v-flex>
          <v-flex
            xs12
            md3
          >
            <v-menu
              ref="menuFim"
              v-model="menuFim"
              :close-on-content-click="false"
              :nudge-right="40"
              :return-value.sync="parametrizacao.expediente_fim"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              max-width="290px"
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="parametrizacao.expediente_fim"
                  v-validate="'required|is_after:expIni'"
                  name="expediente_fim"
                  data-vv-name="expediente_fim"
                  label="* Fim de expediente"
                  prepend-icon="access_time"
                  :error-messages="errors.first('expediente_fim')"
                  readonly
                  v-on="on"
                />
              </template>
              <v-time-picker
                v-if="menuFim"
                v-model="parametrizacao.expediente_fim"
                format="24hr"
                data-vv-name="expediente_fim"
                full-width
                @click:minute="$refs.menuFim.save(parametrizacao.expediente_fim)"
              />
            </v-menu>
          </v-flex>
          <v-flex xs12>
            <v-text-field
              v-model="parametrizacao.logo"
              v-validate="'required'"
              name="logo"
              label="* Logo"
              :error-messages="errors.first('logo')"
            />
          </v-flex>
          <v-flex
            xs12
            md10
          >
            <v-text-field
              v-model="parametrizacao.endereco"
              v-validate="'required'"
              name="endereco"
              label="* Endereco"
              :error-messages="errors.first('endereco')"
            />
          </v-flex>
          <v-flex
            xs12
            md2
          >
            <v-text-field
              v-model="parametrizacao.numero"
              v-validate="'required'"
              name="numero"
              label="* Número"
              :error-messages="errors.first('numero')"
            />
          </v-flex>
          <v-flex
            xs12
            md9
          >
            <v-text-field
              v-model="parametrizacao.bairro"
              v-validate="'required'"
              name="bairro"
              label="* Bairro"
              :error-messages="errors.first('bairro')"
            />
          </v-flex>
          <v-flex
            xs12
            md3
          >
            <v-text-field
              v-model="parametrizacao.telefone"
              v-validate="'required|min:10'"
              name="telefone"
              label="* Telefone"
              :mask="'(##)####-####'"
              :error-messages="errors.first('telefone')"
            />
          </v-flex>
          <v-flex xs12>
            <v-btn
              color="blue darken-1"
              outline
              dark
              @click="salvar"
            >
              Salvar
            </v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-container>
</template>

<script>
import { Validator } from 'vee-validate';
import Parametrizacao from '../../services/parametrizacao';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  data: () => ({
    menuInicio: false,
    menuFim: false,
    alert: false,
    message: '',
    progress: false,
    id: 0,
    parametrizacao: {
      nome_empresa: '',
      expediente_inicio: '',
      expediente_fim: '',
      logo: '',
      endereco: '',
      numero: '',
      bairro: '',
      telefone: '',
    },
    dictionary: {
      custom: {
        nome_empresa: { required: 'O nome da empresa deve ser informado' },
        expediente_inicio: { required: 'O horário que se inicia o expediente deve ser informado' },
        expediente_fim: {
          required: 'O horário que encerra o expediente deve ser informado',
          is_after: 'O horário que encerra o expediente deve se maior que o horário que se inicia do expediente',
        },
        logo: { required: 'O logo deve ser informado' },
        endereco: { required: 'O endereço deve ser informado' },
        numero: { required: 'O número deve ser infromado' },
        bairro: { required: 'O bairro deve ser informado' },
        telefone: {
          required: 'O telefone deve ser informado',
          min: 'O telefone não foi informado corretamente',
        },
      },
    },
  }),
  created() {
    Validator.extend('is_after', (value, [otherValue]) => this.moment(value, 'HH:mm').isAfter(this.moment(otherValue, 'HH:mm')), {
      hasTarget: true,
    });
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);

    Parametrizacao.show(0).then((result) => {
      if (result.data.length > 0) {
        const data = result.data[0];
        this.id = data.id;
        this.parametrizacao = data;
      }
    });
  },
  methods: {
    isErrors(listErrors = null) {
      this.errors.clear();

      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    salvar() {
      this.alert = false;
      this.progress = true;

      this.$validator.validate().then((valid) => {
        if (valid) {
          if (this.id === 0) {
            Parametrizacao.salvar(this.parametrizacao).then((result) => {
              this.message = 'Parametrização adicionada com sucesso';
              this.alert = true;
            }).catch((error) => {
              if (error.response.data.length > 0) {
                this.isErrors(error.response.data);
              }
            });
          } else {
            Parametrizacao.alterar(this.parametrizacao).then((result) => {
              this.message = 'Parametrização alterada com sucesso';
              this.alert = true;
            }).catch((error) => {
              if (error.response.data.length > 0) {
                this.isErrors(error.response.data);
              }
            });
          }
        }
      });
    },
  },
};
</script>
