<template>
  <center>
    <div class="container-one">
      <v-layout column>
        <v-toolbar
          color="green lighten-4"
          flat
        >
          <v-toolbar-title>
            SAPV - Prefeitura
          </v-toolbar-title>
        </v-toolbar>
        <v-card
          flat
          style="padding: 10px"
        >
          <v-alert
            :value="alert"
            type="error"
            dismissible
          >
            {{ message }}
          </v-alert>
          <v-card-text>
            <v-text-field
              v-model="user.email"
              v-validate="'required|email'"
              prepend-icon="person"
              name="email"
              data-vv-name="email"
              :error-messages="errors.first('email')"
              label="E-mail"
              type="text"
            />
            <v-text-field
              v-model="user.password"
              v-validate="'required'"
              prepend-icon="lock"
              name="password"
              data-vv-name="password"
              :error-messages="errors.first('password')"
              label="Senha"
              type="password"
            />
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn
              color="#000"
              outline
              @click="login"
            >
              Entrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-layout>
    </div>
  </center>
</template>

<script>
import Auth from '../../services/auth';
import User from '../../services/users';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  name: 'Login',
  data: () => ({
    alert: false,
    user: {
      email: '',
      password: '',
    },
    message: '',
    dictionary: {
      custom: {
        email: {
          required: 'E-mail não foi informado',
          email: 'E-mail não foi informado corretamente',
        },
        password: {
          required: 'Senha não foi informada',
        },
      },
    },
  }),
  mounted() {
    this.$validator.localize('en', this.dictionary);
  },
  methods: {
    login() {
      this.alert = false;
      this.$validator.validate().then((valid) => {
        if (valid) {
          Auth.login(this.user).then((result) => {
            if (result.data !== '') {
              this.$store.commit('updatedToken', result.data.token);
              this.$store.commit('updatedUser', result.data.user);
              localStorage.setItem('token', result.data.token);

              this.$router.replace('/');
              this.alert = false;
            } else {
              this.alert = true;
              this.message = 'Usuário ou senha inválidos';
            }
          }).catch((err) => {
            if (err.length > 0) {
              err.foreach((item) => {
                this.errors.add({
                  field: item.field,
                  msg: item.msg,
                });
              });
            }
          });
        }
      });
    },
  },
};
</script>

<style>
.container-one {
  width: 500px;
  margin-top: 10%;
}
</style>
