<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Cid 10
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="50%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Cid 10
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="cid.nome"
                  name="nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                />
              </v-flex>
              <v-flex
                xs12
                md8
              >
                <v-select
                  v-model=" cid.especialidade_id"
                  name="especialidade_id"
                  label="* Especialidade"
                  :items="especialidades"
                  item-text="nome"
                  item-value="id"
                  :error-messages="errors.first('especialidade_id')"
                />
              </v-flex>
              <v-flex
                xs12
                md4
              >
                <v-text-field
                  v-model="cid.codigo"
                  name="codigo"
                  label="* Codigo"
                  :error-messages="errors.first('codigo')"
                />
              </v-flex>
              <v-flex xs12>
                <v-textarea
                  v-model="cid.descricao"
                  name="descricao"
                  label="Descrição"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar este Cid10?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="cids"
            :search="search"
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.codigo }}
              </td>
              <td class="text-xs-left">
                {{ props.item.especialidade.nome }}
              </td>
              <td class="text-xs-left">
                {{ props.item.descricao }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-cid />
  </v-container>
</template>

<script>
import Cid from '../../services/cids';
import Especialidade from '../../services/especialidades';
import HelpCid from './HelpCid.vue';

export default {
  components: {
    HelpCid,
  },
  data: () => ({
    alert: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    search: '',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'Codigo', value: 'codigo' },
      { text: 'Especialidade', value: 'especialidade.nome' },
      { text: 'Descrição', value: 'descricao' },
    ],
    id: 0,
    cid: {
      nome: '',
      codigo: '',
      especialidade: null,
      especialidade_id: 0,
      descricao: '',
    },
    cids: [],
    especialidades: [],
  }),
  mounted() {
    this.listarCids();
    this.listarEspecialidades();
  },
  methods: {
    listarCids() {
      Cid.listar().then((result) => {
        this.cids = result.data;
      });
    },
    listarEspecialidades() {
      Especialidade.listar().then((result) => {
        this.especialidades = result.data;
      });
    },
    isErrors(listErrors) {
      this.errors.clear();
      this.progress = false;

      listErrors.forEach((err) => {
        this.errors.add({
          field: err.field,
          msg: err.message,
        });
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.cid = {};
      this.errors.clear();
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Cid.salvar(this.cid).then((result) => {
          this.limpar();
          this.listarCids();

          this.message = 'Cid 10 adicionado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      } else {
        Cid.alterar(this.cid).then((result) => {
          this.limpar();
          this.listarCids();

          this.message = 'Cid 10 alterado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.isErrors(error.response.data);
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;
      this.cid = item;
      this.dialog = true;
    },
    messageDelete(item) {
      this.cid = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Cid.deletar(this.cid).then((result) => {
        this.message = 'Cid 10 deletado com sucesso';
        this.alert = true;

        this.listarCids();
      }).catch((error) => {
        console.log(error);
      });
      //Impossivel excluir este Cid 10, pois o mesmo está vinculado à agendamentos
 
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
