import { http } from './config';

export default {
  salvar: bloqueio => http.post('bloqueios', bloqueio),
  alterar: bloqueio => http.put(`bloqueios/${bloqueio.id}`, bloqueio),
  deletar: bloqueio => http.delete(`bloqueios/${bloqueio.id}`),
  listar: () => http.get('bloqueios'),
  listarPorVeiculo: id => http.get(`veiculos/${id}/bloqueios`),
};
