import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Veiculo from './views/veiculos/Index.vue';
import Paciente from './views/pacientes/Index.vue';
import Motorista from './views/motoristas/Index.vue';

import Agendamento from './views/agendamentos/Index.vue';
import AgendamentoTodos from './views/agendamentos/Todos.vue';

import Index from './views/viagens/Index.vue';
import Emergencia from './views/viagens/Emergencia.vue';
import Fixa from './views/viagens/Fixa.vue';

import Bloqueio from './views/bloqueios/Index.vue';
import Saida from './views/saidas/Index.vue';
import Retorno from './views/retornos/Index.vue';
import Especialidade from './views/especialidades/Index.vue';
import Procedimento from './views/procedimentos/Index.vue';
import Localidade from './views/localidades/Index.vue';
import Local from './views/locaisConsulta/Index.vue';
import Cid from './views/cids/Index.vue';
import Login from './views/account/Login.vue';
import Parametrizacao from './views/parametrizacao/Index.vue';

import Relatorio from './views/relatorio/Index.vue';
import TestePdf from './views/testePdf.vue';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/login',
      component: Login,
      meta: {
        guest: true,
      },
    },
    {
      path: '/',
      component: Home,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/viagens',
      component: Index,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/viagens/fixa',
      component: Fixa,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/viagens/emergencia',
      component: Emergencia,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/viagens/emergencia/:id',
      component: Emergencia,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/agendamentos/agendar-pacientes',
      component: Agendamento,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/agendamentos/todos',
      component: AgendamentoTodos,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/veiculos',
      component: Veiculo,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/pacientes',
      component: Paciente,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/bloqueio-veiculos',
      component: Bloqueio,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/saida-veiculos',
      component: Saida,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/retorno-veiculos',
      component: Retorno,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/motoristas',
      component: Motorista,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/especialidades',
      component: Especialidade,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/procedimentos',
      component: Procedimento,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/localidades',
      component: Localidade,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/locais-de-consulta',
      component: Local,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/cids',
      component: Cid,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/relatorio',
      component: Relatorio,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/teste-pdf',
      component: TestePdf,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: '/parametrizacao',
      component: Parametrizacao,
      meta: {
        requiresAuth: true,
      },
    },
  ],
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('token') == null) {
      next({
        path: '/login',
        params: { nextUrl: to.fullPath },
      });
    } else {
      next();
    }
  } else if (to.matched.some(record => record.meta.guest)) {
    if (localStorage.getItem('token') == null) {
      next();
    } else {
      next({ path: '/' });
    }
  } else if (localStorage.getItem('token') == null) {
    next();
  } else {
    next({ path: '/' });
  }
});

export default router;
