<template>
  <v-container>
    <v-alert
      v-model="alert"
      transition="scale-transition"
      origin="center center"
      dismissible
      dark
      class="success"
      type="success"
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Saida de Veículos
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="65%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Saida
          </v-toolbar-title>
        </v-toolbar>
        <v-card flat>
          <v-container grid-list-md>
            <v-alert
              v-model="alertW"
              transition="scale-transition"
              origin="center center"
              dismissible
              dark
              class="warning"
              type="warning"
            >
              {{ messageW }}
            </v-alert>
            <v-alert
              v-model="alertE"
              transition="scale-transition"
              origin="center center"
              dismissible
              dark
              class="error"
              type="error"
            >
              {{ message }}
            </v-alert>
            <v-layout
              row
              wrap
            >
              <v-flex
                xs12
                md4
              >
                <input
                  ref="dataAtual"
                  v-model="dataAtual"
                  type="text"
                  hidden
                >
                <v-menu
                  v-model="menuData"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="dataSaidaFormated"
                    v-validate="'required|after:dataAtual|date_format:DD/MM/YYYY'"
                    data-vv-name="data_saida"
                    name="data_saida"
                    :error-messages="errors.first('data_saida')"
                    label="* Data"
                    prepend-icon="event"
                    readonly
                  />
                  <v-date-picker
                    v-model="movimentacao.data_saida"
                    locale="pt-BR"
                    data-vv-name="data_saida"
                    @input="menuData = false"
                  />
                </v-menu>
              </v-flex>
              <v-flex xs12>
                <v-autocomplete
                  v-model="movimentacao.viagem_id"
                  v-validate="'required'"
                  :items="listaViagens"
                  name="viagem_id"
                  data-vv-name="viagem_id"
                  box
                  chips
                  color="blue-grey lighten-2"
                  label="* Viagens"
                  item-text="datHorLoc"
                  item-value="id"
                  :error-messages="errors.first('viagem_id')"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      :selected="data.selected"
                      close
                      class="chip--select-multi"
                      @input="remove(data.item)"
                    >
                      {{ data.item.data }} -
                      {{ data.item.horario }} -
                      {{ data.item.localidade.nome }}
                    </v-chip>
                  </template>
                  <template v-slot:item="data">
                    <template v-if="typeof data.item !== 'object'">
                      <v-list-tile-content v-text="data.item" />
                    </template>
                    <template v-else>
                      <v-list-tile-avatar v-if="data.item.tipo == 'Fixa'">
                        <v-icon>
                          history
                        </v-icon>
                      </v-list-tile-avatar>
                      <v-list-tile-avatar v-if="data.item.tipo == 'Emergencia'">
                        <v-icon>
                          report
                        </v-icon>
                      </v-list-tile-avatar>
                      <v-list-tile-content>
                        <v-list-tile-sub-title v-html="`<b>Data:</b> ${data.item.data}  <b>Horário:</b> ${data.item.horario}  <b>Localidade:</b> ${data.item.localidade.nome}`" />
                        <v-list-tile-sub-title v-html="`<b>Motorista:</b> ${data.item.motorista.nome}`" />
                      </v-list-tile-content>
                    </template>
                  </template>
                </v-autocomplete>
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-menu
                  ref="menu"
                  v-model="menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  :return-value.sync="movimentacao.horario_saida"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  max-width="290px"
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="movimentacao.horario_saida"
                      v-validate="'required'"
                      name="horario_saida"
                      data-vv-name="horario_saida"
                      label="* Horário da saída"
                      prepend-icon="access_time"
                      :error-messages="errors.first('horario_saida')"
                      readonly
                      v-on="on"
                    />
                  </template>
                  <v-time-picker
                    v-if="menu2"
                    v-model="movimentacao.horario_saida"
                    format="24hr"
                    data-vv-name="horario_saida"
                    full-width
                    @click:minute="$refs.menu.save(movimentacao.horario_saida)"
                  />
                </v-menu>
              </v-flex>
              <v-flex
                xs12
                md3
              >
                <v-text-field
                  v-model="movimentacao.quilometragem_saida"
                  v-validate="'required'"
                  data-vv-name="quilometragem_saida"
                  name="quilometragem_saida"
                  label="Quilometragem atual"
                  :error-messages="errors.first('quilometragem_saida')"
                  hint="km"
                  type="number"
                  min="0"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="saidas"
            :search="search"
          >
            <template
              slot="items"
              slot-scope="props"
            >
              <td class="text-xs-left">
                {{ props.item.viagem.veiculo.numero }} - {{ props.item.viagem.veiculo.modelo }}
              </td>
              <td class="text-xs-left">
                {{ props.item.viagem.localidade.nome }}
              </td>
              <td class="text-xs-left">
                {{ moment(props.item.data_saida).format('DD/MM/YYYY') }}
              </td>
              <td class="text-xs-left">
                {{ moment(props.item.horario_saida, 'HH:mm').format('HH:mm') }}
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-saida />
  </v-container>
</template>

<script>
import { Validator } from 'vee-validate';
import Veiculo from '../../services/veiculos';
import Viagem from '../../services/viagens';
import Saida from '../../services/saidas';
import HelpSaida from './HelpSaida.vue';

export default {
  $_veeValidate: {
    validator: 'new',
  },
  components: {
    HelpSaida,
  },
  data: () => ({
    alert: false,
    alertE: false,
    message: '',
    search: '',
    alertW: false,
    messageW: '',
    time: null,
    menu2: false,
    menuData: false,
    panel: [true, true, false],
    disabled: false,
    dialog: false,
    mask_placa: 'AAA-####',
    headers: [
      { text: 'Veiculo', value: 'modelo' },
      { text: 'Localidade', value: 'nome' },
      { text: 'Data saida', value: 'data_saida' },
      { text: 'Horário saida', value: 'horario_saida' },
    ],
    dataAtual: null,
    listaViagens: [],
    listaVeiculos: [],
    saidas: [],
    movimentacao: {
      data_saida: null,
      horario_saida: null,
      quilometragem_saida: null,
      viagem_id: null,
    },
    viagem: {
      id: '',
      data: null,
      horario: null,
      localidade: { nome: null },
      motorista: { nome: '' },
      veiculo_id: null,
      tipo: null,
      status: null,
    },
    veiculo: {
      id: null,
      numMod: null,
      quilometragem_atual: null,
      quilometragem_anterior: null,
    },
    hrViagem: null,
    dictionary: {
      custom: {
        data_saida: {
          required: 'Informe a data de saída',
          after: 'Data de saída deve ser maior ou igual a data atual',
        },
        horario_saida: {
          required: 'Informe o horário de saída',
        },
        quilometragem_saida: {
          required: 'Informe a quilometragem atual na saída',
        },
        viagem_id: {
          required: 'Informe a viagem a ser realizada pelo veículo',
        },
      },
    },
  }),
  computed: {
    dataSaidaFormated() {
      return this.movimentacao.data_saida != null ? this.moment(this.movimentacao.data_saida).format('DD/MM/YYYY') : null;
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'movimentacao.data_saida': function () {
      this.listarViagens();
    },
    // eslint-disable-next-line func-names
    'movimentacao.viagem_id': function () {
      if (this.movimentacao.viagem_id != null) {
        Viagem.show(this.movimentacao.viagem_id).then((result) => {
          this.hrViagem = this.moment(this.viagem.horario, 'HH:mm').format('HH:mm');
          this.movimentacao.horario_saida = null;
          if (result.data.agendamentos.length === 0) {
            this.viagem = null;
            this.errors.add({
              field: 'viagem_id',
              msg: 'Não existe pacientes agendados para está viagem',
            });
          } else {
            this.viagem = result.data;
          }
        });
      }
    },
    // eslint-disable-next-line func-names
    'movimentacao.horario_saida': function () {
      if (this.moment(this.movimentacao.horario_saida, 'HH:mm').isAfter(this.moment(this.viagem.horario, 'HH:mm'))) {
        this.alertW = true;
        this.messageW = 'Viagem está com horário de saída em atraso!';
      }
    },
  },
  mounted() {
    this.$validator.localize('en', this.dictionary);
    this.movimentacao.data_saida = new Date().toISOString().substr(0, 10);

    this.listarSaidas();
    this.listarVeiculos();
    this.dataAtual = this.moment(this.moment().subtract(1, 'days')).format('DD/MM/YYYY');
  },
  methods: {
    remove(item) {
      this.movimentacao.viagem_id = null;
    },
    listarSaidas() {
      const movimento = 'saida';
      Saida.listar(movimento).then((result) => {
        this.saidas = result.data;
      });
    },
    listarVeiculos() {
      Veiculo.listar().then((result) => {
        result.data.forEach((item) => {
          this.veiculo = {
            id: item.id,
            numMod: `${item.numero} - ${item.modelo}`,
          };
          this.listaVeiculos.push(this.veiculo);
        });
      });
    },
    listarViagens() {
      this.listaViagens = [];

      Viagem.buscarPorVeiculoPeriodo(this.movimentacao.data_saida)
        .then((result) => {
          result.data.forEach((item) => {
            this.viagem = item;
            this.viagem.data = this.moment(item.data).format('DD/MM/YYYY');
            this.listaViagens.push(this.viagem);
          });
        });
    },
    limpar() {
      this.$validator.reset();
      this.dialog = false;
      this.alert = false;
      this.messageE = '';

      this.movimentacao = {};
      this.hrViagem = null;
      this.listaViagens = [];
    },
    salvar() {
      this.$validator.validate().then((valid) => {
        if (this.moment(this.dataSaidaFormated, 'DD/MM/YYYY').isAfter(this.moment(this.moment(new Date()).format('DD/MM/YYYY'), 'DD/MM/YYYY'))) {
          this.errors.add({
            field: 'data_saida',
            msg: 'Data da saída não pode ser depois da data atual',
          });
        }
        if (this.viagem == null) {
          this.errors.add({
            field: 'viagem_id',
            msg: 'Não existe pacientes agendados para está viagem',
          });
        }
        if (this.veiculo.id != null) {
          Veiculo.show(this.veiculo.id).then((result) => {
            this.veiculo = result.data;
            if (this.movimentacao.quilometragem_saida < this.veiculo.quilometragem_atual) {
              this.errors.add({
                field: 'quilometragem_saida',
                msg: 'Quilometragem inferior a quilometragem registrada atualmente',
              });
              valid(false);
            }
          });
        }
        if (valid) {
          this.movimentacao.veiculo_id = this.viagem.veiculo_id;
          Saida.salvar(this.movimentacao).then((result) => {
            this.viagem.status = 'Andamento';
            Viagem.alterar(this.viagem).then((rsViagem) => {
              this.veiculo.quilometragem_anterior = this.veiculo.quilometragem_atual;
              const km = this.movimentacao.quilometragem_saida - this.veiculo.quilometragem_atual;
              this.veiculo.quilometragem_atual = this.veiculo.quilometragem_atual + km;
              Veiculo.alterar(this.veiculo).then((resultVeiculo) => {
                this.message = 'Saída de veículo realizada com sucesso';
                this.alert = true;

                this.limpar();
                this.listarSaidas();
              });
            });
          });
        }
      });
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
