<template>
  <v-container>
    <v-alert
      :value="alert"
      type="success"
      transition="scale-transition"
      dismissible
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Procedimentos
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="50%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Procedimentos
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="procedimento.nome"
                  name="Nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  v-model="procedimento.descricao"
                  name="Descricao"
                  label="Descrição"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar este Procedimento?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="procedimentos"
            :search="search"
          >
            <template v-slot:items="props">
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.descricao }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-procedimento />
  </v-container>
</template>

<script>
import Procedimento from '../../services/procedimentos';
import HelpProcedimento from './HelpProcedimento.vue';

export default {
  components: { HelpProcedimento },
  data: () => ({
    alert: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    search: '',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'Descrição', value: 'descricao' },
    ],
    id: 0,
    procedimento: {
      nome: '',
      descricao: '',
    },
    procedimentos: [],
  }),
  mounted() {
    this.listarProcedimentos();
  },
  methods: {
    listarProcedimentos() {
      Procedimento.listar().then((result) => {
        this.procedimentos = result.data;
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.procedimento = {};
      this.errors.clear();
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Procedimento.salvar(this.procedimento).then((result) => {
          this.limpar();
          this.listarProcedimentos();

          this.message = 'Procedimento adicionado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.errors.add({
              field: error.response.data[0].field,
              msg: error.response.data[0].message,
            });
          }
        });
      } else {
        Procedimento.alterar(this.procedimento).then((result) => {
          this.limpar();
          this.listarProcedimentos();

          this.message = 'Procedimento alterado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.errors.add({
              field: error.response.data[0].field,
              msg: error.response.data[0].message,
            });
          }
        });
      }
    },
    cancelar() {
      this.limpar();
    },
    messageDelete(item) {
      this.procedimento = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Procedimento.deletar(this.procedimento).then((result) => {
        this.alert = true;
        this.message = 'Procedimento deletado com sucesso';

        this.listarProcedimentos();
      });
      this.limpar();
    },
    editar(item) {
      this.id = item.id;
      this.procedimento = item;
      this.dialog = true;
    },
  },
};
</script>
