<template>
  <v-container>
    <v-alert
      v-model="alert"
      color="success"
      type="success"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-alert
      v-model="alertError"
      color="error"
      type="error"
      dismissible
      dark
    >
      {{ message }}
    </v-alert>
    <v-toolbar
      flat
      color="green lighten-4"
    >
      <v-toolbar-title class="font-weight-light title text-uppercase">
        Especialidades
      </v-toolbar-title>
      <v-spacer />
      <v-dialog
        v-model="dialog"
        max-width="50%"
        persistent
        transition="dialog-transition"
      >
        <v-btn
          slot="activator"
          flat
          dark
          icon
          outline
          color="primary"
          class="mb-2"
        >
          <v-icon>add</v-icon>
        </v-btn>
        <v-toolbar
          flat
          color="green lighten-4"
        >
          <v-toolbar-title class="font-weight-light title text-uppercase">
            Gerenciar Especialidades
          </v-toolbar-title>
          <v-spacer />
          <template v-if="progress">
            <v-progress-circular
              :size="30"
              :width="5"
              color="green"
              indeterminate
            />
          </template>
        </v-toolbar>
        <v-card>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="especialidade.nome"
                  name="nome"
                  label="* Nome"
                  :error-messages="errors.first('nome')"
                  data-vv-as="nome"
                  data-vv-name="nome"
                />
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  v-model="especialidade.descricao"
                  name="descricao"
                  label="Descrição"
                />
              </v-flex>
              <v-flex xs12>
                <v-btn
                  color="blue darken-1"
                  flat
                  outline
                  @click="salvar"
                >
                  Salvar
                </v-btn>
                <v-btn
                  color="red darken-1"
                  flat
                  outline
                  @click="cancelar"
                >
                  Cancelar
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <!-- Mensagem de confirmação para deletar-->
    <template>
      <div>
        <v-layout
          row
          justify-center
        >
          <v-dialog
            v-model="dialogConfirmDelete"
            persistent
            max-width="290"
          >
            <v-card>
              <v-card-title
                class="error headline"
                style="font-weight: bold; color: white"
              />
              <v-card-text>
                Você deseja realmente deletar esta especialidade?
              </v-card-text>

              <v-card-actions>
                <v-spacer />
                <v-btn
                  flat
                  @click="limpar()"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  flat
                  color="error darken-1"
                  @click="deletar()"
                >
                  Confirmar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </div>
    </template>
    <template>
      <v-card flat>
        <v-card-title>
          <v-spacer />
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          />
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="especialidades"
            :search="search"
          >
            <template v-slot:items="props">
              <td>{{ props.item.nome }}</td>
              <td class="text-xs-left">
                {{ props.item.descricao }}
              </td>
              <td class="justify-center layout px-0">
                <v-btn
                  small
                  outline
                  icon
                  @click="editar(props.item)"
                >
                  <v-icon small>
                    edit
                  </v-icon>
                </v-btn>
                <v-btn
                  small
                  outline
                  icon
                  @click="messageDelete(props.item)"
                >
                  <v-icon small>
                    delete
                  </v-icon>
                </v-btn>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </template>
    <help-especialidade />
  </v-container>
</template>

<script>
import Especialidade from '../../services/especialidades';
import HelpEspecialidade from './HelpEspecialidade.vue';

export default {
  components: { HelpEspecialidade },
  data: () => ({
    alert: false,
    alertError: false,
    dialog: false,
    dialogConfirmDelete: false,
    progress: false,
    message: '',
    search: '',
    headers: [
      { text: 'Nome', value: 'nome' },
      { text: 'Descrição', value: 'descricao' },
    ],
    id: 0,
    especialidade: {
      nome: '',
      descricao: '',
    },
    especialidades: [],
  }),
  mounted() {
    this.listarEspecialidades();
  },
  methods: {
    listarEspecialidades() {
      Especialidade.listar().then((result) => {
        this.especialidades = result.data;
      });
    },
    limpar() {
      this.alert = false;
      this.dialog = false;
      this.dialogConfirmDelete = false;
      this.progress = false;

      this.id = 0;
      this.especialidade = {};
      this.errors.clear();
    },
    salvar() {
      this.progress = true;

      if (this.id === 0) {
        Especialidade.salvar(this.especialidade).then((result) => {
          this.limpar();
          this.listarEspecialidades();

          this.message = 'Especialidade adicionado com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.progress = false;
            this.errors.add({
              field: error.response.data[0].field,
              msg: error.response.data[0].message,
            });
          }
        });
      } else {
        Especialidade.alterar(this.especialidade).then((result) => {
          this.limpar();
          this.listarEspecialidades();

          this.message = 'Especialidade alterada com sucesso';
          this.alert = true;
        }).catch((error) => {
          if (error.response.data.length > 0) {
            this.progress = false;
            this.errors.add({
              field: error.response.data[0].field,
              msg: error.response.data[0].message,
            });
          }
        });
      }
    },
    editar(item) {
      this.id = item.id;

      this.especialidade = item;
      this.dialog = true;
    },
    messageDelete(item) {
      this.especialidade = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Especialidade.deletar(this.especialidade).then((result) => {
        if (result.data) {
          this.alert = true;
          this.message = 'Especialidade deletada com sucesso';

          this.listarEspecialidades();
        } else {
          this.alertError = true;
          this.message = 'Não é possivel deletar, pois a especialidade está vinculada a um ou mais cids';
        }
      });
      this.limpar();
    },
    cancelar() {
      this.limpar();
    },
  },
};
</script>
