import { http } from './config';

export default {
  salvar: viagem => http.post('viagens', viagem),
  alterar: viagem => http.put(`viagens/${viagem.id}`, viagem),
  deletar: viagem => http.delete(`viagens/${viagem.id}`),
  show: id => http.get(`viagens/${id}`),
  listar: () => http.get('viagens'),
  buscarPorPeriodo: (dateIni, dateFin, id = 0) => http.get(`viagens/${id}/periodo`, { params: { dateIni, dateFin } }),
  buscarPorVeiculoStatus: (veiculo, status) => http.get(`veiculos/${veiculo}/viagens/${status}`),
  buscarPorVeiculoPeriodo: (dateIni = null, dateFin = null, veiculo = 0, horario = null, id = 0) => http.get(`veiculos/${veiculo}/viagens`, {
    params: {
      dateIni,
      dateFin,
      horario,
      id,
    },
  }),
  buscarPorMotoristaPeriodo: (dateIni = null, dateFin = null, motorista, horario = null, id = 0) => http.get(`motoristas/${motorista}/viagens`, {
    params: {
      dateIni,
      dateFin,
      horario,
      id,
    },
  }),
  buscarPorLocalidadeData: (data, localidade = 0) => http.get(`localidades/${localidade}/viagens`, {
    params: {
      data,
    },
  }),
};
