<script>
import { isNull } from 'util';
import Agendamento from '../../services/agendamentos';

export default {
  data: () => ({
    search: '',
    dialogDetalhes: false,
    agendamento: {
      id: null,
      paciente_id: null,
      paciente: { nome: '', celular: '' },
      data: null,
      horario: null,
      local_de_consulta_id: null,
      localDeConsulta: { nome: '' },
      especialidade_id: null,
      especialidade: { nome: '' },
      cid_id: null,
      cid: { nome: '' },
      procedimentos: [],
      viagem_id: null,
      viagem: {
        data: null,
        motorista: { nome: '', celular: '' },
        localidade: { nome: '' },
        veiculo: { numMod: '' },
      },
      viagemVeiculo: null,
    },
    headers: [
      { text: 'Paciente - Celular', value: 'nome' },
      { text: 'Data - Horário', value: 'data' },
      { text: 'Especialidade', value: 'nome' },
      { text: 'Local de consulta', value: 'nome' },
    ],
    agendamentos: [],
  }),
  mounted() {
    this.listarAgendamentos();
  },
  methods: {
    listarAgendamentos() {
      Agendamento.listar().then((result) => {
        this.agendamentos = result.data.map((item) => {
          const agendamento = item;
          agendamento.paciente.celular = `(${agendamento.paciente.celular.substr(0, 2)}) ${agendamento.paciente.celular.substr(2, 5)} - ${agendamento.paciente.celular.substr(7)}`;
          agendamento.viagem.data = this.moment(agendamento.viagem.data).format('DD/MM/YYYY');
          return agendamento;
        });
      });
    },
    detalhes(item) {
      this.agendamento = item;
      console.log(item.id, ':::', item.procedimentos);

      this.agendamento.paciente.celular = `(${item.paciente.celular.substr(0, 2)})${item.paciente.celular.substr(2, 5)}-${item.paciente.celular.substr(7, 11)}`;
      this.agendamento.viagem.veiculo.numMod = `${item.viagem.veiculo.numero} - ${item.viagem.veiculo.modelo} - ${item.viagem.veiculo.capacidade} `;
      this.agendamento.viagem.motorista.ceular = `(${item.viagem.motorista.celular.substr(0, 2)})${item.viagem.motorista.celular.substr(2, 5)}-${item.viagem.motorista.celular.substr(7, 11)}`;
      this.agendamento.viagem.data = this.moment(this.agendamento.viagem.data, 'YYYY-MM-DD').format(['DD/MM/YYYY']);
      this.agendamento.viagem.horario = this.moment(this.agendamento.viagem.horario, 'HH:mm:ss').format('HH:mm');
      this.dialogDetalhes = true;

      console.log(this.agendamento);
    },
    messageDelete(item) {
      this.agendamento = item;
      this.dialogConfirmDelete = true;
    },
    deletar() {
      Agendamento.deletar(this.agendamento).then((result) => {
        if (result.data) {
          this.alertS = true;
          this.message = 'Agendamento deletado com sucesso';

          this.oucupados = this.oucupados - 1;
          this.vagos = this.capacidade - this.oucupados;

          this.listarAgendamentos();
          this.dialogConfirmDelete = false;
          this.limparAgendamento();
        }
      });
    },
  },
};
</script>
