import { http } from './config';

export default {
  salvar: veiculo => http.post('veiculos', veiculo),
  show: id => http.get(`veiculos/${id}`),
  alterar: veiculo => http.put(`veiculos/${veiculo.id}`, veiculo),
  deletar: veiculo => http.delete(`veiculos/${veiculo.id}`),
  listar: (bloqueio = null) => http.get('veiculos', bloqueio),
};
